import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";

import Theme from "../../Theme";
import Banners from "../Banners";
import GoogleAd from "./utils/GoogleAd"; //TODO definir melhor o path do utils

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        margin: "20px",
        padding: "20px",
    },
}));

export const DefaultBanners = (props) => {
    const classes = useStyles();
    const { isMobile } = props;

    const carouselTop = [
        {
            image:
        "/21624048414/Sub_Viagens_Home_Hotel_V2.1_Banner_Responsivo_Carrosel_P01_625x250_328x140",
            dimensions: [[625, 250], [328, 140]],
            tag: "div-gpt-ad-1570037345511-0",
        },
        {
            image:
        "/21624048414/Sub_Viagens_Home_Hotel_V2.1_Banner_Responsivo_Carrosel_P02_625x250_328x140",
            dimensions: [[625, 250], [328, 140]],
            tag: "div-gpt-ad-1570036524292-0",
        },
    ];

    const bannerTop = [
        {
            image: "/21624048414/Sub_Viagens_Home_Hotel_V2.1_Banner_Responsivo_Inline_P01_300x250",
            dimensions: [[300, 250], [160, 160]],
            tag: "div-gpt-ad-1570035323953-0",
        },
        {
            image: "/21624048414/Sub_Viagens_Home_Hotel_V2.1_Banner_Responsivo_Inline_P02_300x250",
            dimensions: [[300, 250], [160, 160]],
            tag: "div-gpt-ad-1570037841122-0",
        },
    ];

    GoogleAd({ dfps: carouselTop, isMobile });
    GoogleAd({ dfps: bannerTop, isMobile });

    return (
        <Paper className={classes.root} id="banner">
            <Typography variant="h3">Banners</Typography>
            <br />
            <Divider />
            <br />

            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Typography type="headline3">Promoções para sua viagem</Typography>
                </Grid>
                <Grid item xs={12} lg={6}>
                    <Banners dfp={carouselTop} isSlider infinite={false} />
                </Grid>
                <Grid item xs={12} lg={6}>
                    <Banners dfp={bannerTop} />
                </Grid>
            </Grid>
        </Paper>
    );
};

storiesOf("Components/Banners", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
