import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import themes from "../../Theme/themes";
import Theme from "../../Theme";
import Covid19Alert from "../Covid19Alert";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        margin: "20px",
        padding: "20px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        margin: "20px",
        padding: "20px",
    },
}));

export const DefaultCovid19Alert = ({ brand, mode }) => {
    const classes = useStyles();

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="covid19">
                <Typography variant="h3">Alerta Covid</Typography>
                <br />
                <Divider />
                <br />

                <Grid container spacing={2}>
                    <Grid item xs={12} lg={6}>
                        <Covid19Alert />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

storiesOf("Components/CovidAlert", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default ", (args) => <DefaultCovid19Alert {...args} />, {
        args: {
            brand: "CVC",
            mode: "root",
        },
        argTypes: {
            brand: {
                type: "select",
                options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
            },
            mode: {
                type: "select",
                options: ["root", "darkBg"]
            },
        }
    });
