import React, { useContext, useState } from "react";
import { BrandContext } from "../BrandContext/BrandContext";
import { withStyles } from "@material-ui/styles";
import PropTypes from "prop-types";
import { styles } from "./styles";

const Image = props => {
    // TODO rever implementação do contexto
    var company = useContext(BrandContext);
    const imageUrl = company ? `${company.imageProcessorPath}image/fetch` : "";
    const [imgUrl, setImgUrl] = useState(mountSrcUrl(props, imageUrl));
    const [fallbackPosition, setFallbackPosition] = useState(0);
    const { alt, className } = props;

    const onError = () => {
        if (props.fallback || props.fallbackUrl) {
            const properties = { src: props.fallbackUrl ? props.fallbackUrl : props.fallback[fallbackPosition], width: props.width, height: props.height };
            const newImgUrl = mountSrcUrl(properties, imageUrl);
            setImgUrl(newImgUrl);
            props.fallback && setFallbackPosition(prevState => prevState + 1);
        }
    };

    return (
        <>
            <img src={imgUrl} alt={alt} className={className} onError={onError} />
        </>
    );
};

Image.propTypes = {
    className: PropTypes.string,
    alt: PropTypes.string,
    src: PropTypes.string.isRequired,
    fallbackUrl: PropTypes.string,
    fallback: PropTypes.array,
    width: PropTypes.number,
    height: PropTypes.number
};

/**
 * Mounts the url based on the component props
 * The sequence of efects matters! The effect is applyed to the output of the previous one
 */
function mountSrcUrl({ src, width, height }, imageUrl) {
    imageUrl = improveOriginalImage(imageUrl);
    imageUrl = scaleImage(imageUrl, width, height);
    return `${imageUrl}/${src}`;
}

/**
 * Width - adds width and Height dimensions
 */
function scaleImage(path, width, height) {
    const pathSlices = ["c_fill"];
    if (width) pathSlices.unshift(`w_${width}`);
    if (height) pathSlices.unshift(`h_${height}`);
    return `${path}/${pathSlices.join(",")}`;
}

/**
 * Sharpen - Improves contrast without loosing quality
 */
function improveOriginalImage(path) {
    const step1 = ["e_trim"];
    const step2 = ["e_sharpen", "f_auto", "fl_lossy"];
    const steps = [step1.join(","), step2.join(",")];
    return `${path}/${steps.join("/")}`;
}

export default withStyles(styles, { withTheme: true })(Image);
