import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Pin from "../Pin";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type PinStoryProps = {
    mode: string,
    brand: BrandType,
    color: "light" | "dark"
    label: string
}

const PinStory = ({
    mode, brand, color, label
}: PinStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Pin">
                <Typography variant="h3">Pin</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={4}>
                    <Pin
                        classes={classes}
                        color={color}
                        className=""
                    >
                        {label}
                    </Pin>

                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        color: "light",
        label: "Texto de exemplo!"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        color: {
            options: ["light", "dark"],
            control: { type: "radio" }
        },
        label: { type: "string" }
    }
}

storiesOf("Components/Pin", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <PinStory {...args}/>, argsControls);
