import React, { useState } from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import Button from "../../Button";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Snackbar from "../Snackbar";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type SnackbarStoryProps = {
    mode: string
    brand: BrandType
    hasIcon: boolean
    hasBtn: boolean
    type: "success" | "error"
    labelClose: string
    message: string
}

const SnackbarStory = ({
    mode, brand, hasIcon, hasBtn, type, labelClose, message
}: SnackbarStoryProps) => {
    const classes = useStyles(themes[brand]);
    const [handleSuccess, setHandleSuccess] = useState(false);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Snackbar">
                <Typography variant="h3">Snackbar</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={3}>
                    <Button
                        color="primary"
                        label="Show"
                        size="medium"
                        variant="contained"
                        onClick={() => setHandleSuccess(!handleSuccess)}
                    />
                </Grid>
                <Grid item xs={2}>
                    <Snackbar
                        message={message}
                        isOpen={handleSuccess}
                        hasBtn={hasBtn}
                        type={type}
                        hasIcon={hasIcon}
                        labelClose={labelClose}
                        close={() => setHandleSuccess(!handleSuccess)}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        hasIcon: true,
        hasBtn: true,
        type: "success",
        labelClose: "Fechar",
        message: "Greyhound divisively hello"

    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        hasIcon: { type: "boolean" },
        hasBtn: { type: "boolean", },
        type: {
            control: { type: "radio" },
            options: ["success", "error"]
        },
        labelClose: { type: "string" },
        message: { type: "string" },
    }
}

storiesOf("Components/Snackbar", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <SnackbarStory {...args}/>, argsControls);
