import React from "react";
import { Grid } from "@material-ui/core";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import ThemeProviderCustom from "../../ThemeProviderCustom";
import { BrandType } from "../../types/brand";
import mockBrands from "../../utils/brandMock";
import CoreHeader from "../CoreHeader";

interface ICoreHeader  {
    brand: BrandType,
    isMobile: boolean,
    showCovidAlert: boolean,
}

const CoreHeaderStory = ({ brand, isMobile, showCovidAlert }: ICoreHeader) => (
    <BrandContext.Provider value={mockBrands[brand]}>
        <ThemeProviderCustom>
            <Grid item xs={isMobile ? 2 : 12}>
                <CoreHeader
                    isMobile={isMobile}
                    showCovidAlert={showCovidAlert}                    
                />
            </Grid>
        </ThemeProviderCustom>
    </BrandContext.Provider>
);

const argsControls = {
    args: {
        brand: "cvc",
        isMobile: false,
        showCovidAlert: true,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        },
        isMobile: { type: "boolean" },
        showCovidAlert: { type: "boolean" },
    }
}

storiesOf("Components/CoreHeader", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <CoreHeaderStory {...args}/>, argsControls);
