import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import Theme from "../../Theme";

import SearchHotel from "../SearchHotel";
import "react-dates/lib/css/_datepicker.css";
// import "../../assets/css/calendar.scss";

const useStyles = makeStyles(theme => ({
    root: {
        boxShadow: theme.shadows[24],
        margin: "20px",
        padding: "20px",
    },
}));

export const DefaultSearchHotel = () => {
    const classes = useStyles();

    return (
        <Paper className={classes.root} id="cards">
            <Typography variant="h3">Search Hotel</Typography>
            <br />
            <Divider />
            <br />

            <SearchHotel isMobile={false} />
        </Paper>
    );
};

storiesOf("Components/Searchs/Search Hotel", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default ", () => <DefaultSearchHotel />);
