import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Link from "../Link";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type LinkStoryProps = {
    mode: string
    brand: BrandType
    link: string
    target: "_blank" | "_self" | "_parent" | "_top" | "framename"
    label: string
    disabled: boolean
}

const LinkStory = ({
    mode, brand, link, target, label, disabled
}: LinkStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Link">
                <Typography variant="h3">Link</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <Link
                        classes={classes}
                        href={link}
                        target={target}
                        disabled={disabled}
                    ><Typography>{label}</Typography></Link>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        link: "https://www.cvc.com.br/",
        target: "_blank",
        label: "Link to somewhere",
        disabled: false
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        link: { type: "string" },
        target: {
            type: "select",
            options: ["_blank", "_self", "_parent", "_top"]
        },
        label: { type: "string" },
        disabled: { type: "boolean" },
    }
}

storiesOf("Components/Buttons/Link", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <LinkStory {...args}/>, argsControls);
