import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Checkbox from "../Checkbox";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type CheckboxStoryProps = {
    label: string,
    mode: string,
    brand: BrandType,
    checked: boolean,
    inverted: boolean,
    disabled: boolean,
}

const CheckboxStory = ({
    label, mode, brand, checked, inverted, disabled
}: CheckboxStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="checkbox">
                <Typography variant="h3">Controles de seleção</Typography>
                <br />
                <Divider />
                <br />
                <Grid container direction="column" justify="center" alignItems="flex-start" spacing={2}>
                    <Grid item xs={2}>
                        <Checkbox
                            classes={classes[mode]}
                            label={label}
                            inverted={inverted}
                            disabled={disabled}
                            checked={checked}
                        />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

storiesOf("Components/Inputs/Checkbox", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Controles de seleção", (args) => <CheckboxStory {...args}/>, {
        args: {
            brand: "CVC",
            label: "Checkbox",
            mode: "root",
            checked: true,
            inverted:  false,
            disabled:  false,
        },
        argTypes: {
            brand: {
                type: "select",
                options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
            },
            label: { type: "string" },
            mode: {
                type: "select",
                options: ["root", "darkBg"]
            },
        }
    });
