import React, { useRef } from "react";
import {
    withScriptjs,
    withGoogleMap,
    GoogleMap,
    Marker,
} from "react-google-maps";
import MarkerClusterer from "react-google-maps/lib/components/addons/MarkerClusterer";
import InfoBox from "react-google-maps/lib/components/addons/InfoBox";
import { Bubbly } from "./styles";
import map from "../assets/img/map.png";
import "./styles.css";

const clusterStyles = [
    {
        url: map,
        height: 72,
        width: 72,
        textColor: "#97007C",
    },
];

const MapGroup = withScriptjs(
    withGoogleMap(
        ({
            defaultData,
            data,
            zoom,
            active,
            handleSelect,
            handleMarkerCluster,
            gridSize,
            maxZoom,
            minimumClusterSize,
            onZoomChanged,
        }) => {
            const mapRefs = useRef(null);

            const onMarkerCluster = cluster => {
                if (handleMarkerCluster) handleMarkerCluster(cluster.getMarkers());
            };

            const handleOnZoomChanged = () => {
                if (onZoomChanged) onZoomChanged(mapRefs.current);
            };

            return data ? (
                <>
                    <GoogleMap
                        defaultZoom={zoom}
                        defaultCenter={{
                            lat: defaultData.coordinates.latitude,
                            lng: defaultData.coordinates.longitude,
                        }}
                        options={{
                            streetViewControl: false,
                            fullscreenControl: false,
                            mapTypeControl: false,
                        }}
                        ref={mapRefs}
                        onZoomChanged={handleOnZoomChanged}
                    >
                        <MarkerClusterer
                            averageCenter
                            enableRetinaIcons
                            gridSize={gridSize}
                            maxZoom={maxZoom}
                            minimumClusterSize={minimumClusterSize}
                            defaultStyles={clusterStyles}
                            onClick={cluster => onMarkerCluster(cluster)}
                        >
                            {data &&
                data.map(item => {
                    return (
                        <Marker
                            title={JSON.stringify(item)}
                            name={item.id}
                            key={item.id}
                            position={{
                                lat: item.coordinates.latitude,
                                lng: item.coordinates.longitude,
                            }}
                            visible={false}
                        >
                            <InfoBox
                                options={{
                                    closeBoxURL: "",
                                    enableEventPropagation: true,
                                }}
                            >
                                <Bubbly
                                    active={active === item.id}
                                    onClick={() => handleSelect(item)}
                                >
                                    <>R$ {item.rate.totalPrice}</>
                                </Bubbly>
                            </InfoBox>
                        </Marker>
                    );
                })}
                        </MarkerClusterer>
                    </GoogleMap>
                </>
            ) : null;
        }
    )
);

export default MapGroup;
