import React, { useRef, useCallback, useEffect, useContext } from "react";
import { FormHandles } from "@unform/core";
import { Auth } from "aws-amplify";
import { IntlShape, injectIntl } from "react-intl";
import * as Yup from "yup";
import { BrandContext } from "../../../BrandContext";
import { Redirect } from "../../../BrandContext/amplify/aws.config"
import { getParam } from "../../../utils";
import useGa from "../../../utils/useGa";
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import { useRenderSteps } from "../../contexts/renderSteps";
import { useToast } from "../../contexts/Toast";
import Form from "../../shared/components/Form";
import HeaderLogin from "../../shared/components/HeaderLogin";
import Input from "../../shared/components/Input";
import InternalButton from "../../shared/components/InternalButton";
import SubTitle from "../../shared/components/SubTitle";
import Terms from "../../shared/components/Terms";
import Title from "../../shared/components/Title";
import { useUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import getValidationErrors from "../../shared/utils/getValidationErrors";
import useStyles from "./styles";
interface IChangePasswrod {
    password: string;
    confirmPassword: string;
}

interface TChangePassword {
    intl: IntlShape;
    onCloseClick?(): void;
    isModal?: boolean;
}

const ChangePassword: React.FC<TChangePassword> = ({ intl, onCloseClick, isModal }) => {
    const { saveOnGa } = useGa();
    const classes = useStyles({});
    const formRef = useRef<FormHandles>(null);
    const { user } = useUserData();
    const ctx = useContext(BrandContext);
    const { amplifyTempPwd } = useAuthAmplifyContext();
    const { addToast } = useToast();

    const { setPreviousStep } = useRenderSteps();
    const successToastMessageTitle = intl.formatMessage({ id: "ChangePassword.successToastMessageTitle" });
    const successToastMessageDescription = intl.formatMessage({ id: "ChangePassword.successToastMessageDescription" });

    useEffect(() => {
        setPreviousStep("ValidationCode");

    }, [setPreviousStep]);

    const handleSubmit = useCallback(async (data: IChangePasswrod): Promise<void> => {
        try {
            saveOnGa({
                event: "login_event",
                category: "Account",
                action: "Recovery Password",
                label: "step 3 | update password"
            });
            formRef.current?.setErrors({});
            const schema = Yup.object().shape({
                password: Yup.string().matches(/^(?=.*[0-9|#?!@$%^&*-]).{8,}$/, intl.formatMessage({ id: "ChangePassword.errorMessage" })),
                confirmPassword: Yup.string().matches(/^(?=.*[0-9|#?!@$%^&*-]).{8,}$/, intl.formatMessage({ id: "ChangePassword.errorMessage" })),
            });

            await schema.validate(data, {
                abortEarly: false
            });
            const { password, confirmPassword } = data;

            if (password !== confirmPassword) {
                formRef.current?.setErrors({ confirmPassword: intl.formatMessage({ id: "ChangePassword.errorMessageNotEqual" }) });
            }
            else {
                Auth.signIn(user.email, amplifyTempPwd).then(async (response) => {
                    try {
                        await Auth.changePassword(response, amplifyTempPwd, password);
                        addToast({ type: "success", title: successToastMessageTitle, description: successToastMessageDescription });
                        saveOnGa({
                            event: "login_event",
                            category: "Account",
                            action: "Recovery Password",
                            label: "updatesenha ok"
                        });

                        if (getParam("path")) {
                            window.location.href = `${Redirect(ctx.hosts)}${getParam("path")}`
                            return
                        }
                        window.location.href = `${Redirect(ctx.hosts)}/mytravels/reservations`;
                    } catch (error) {
                        Auth.signOut();
                        // exibir erro servidor ao trocar senha
                    }
                });
            }

        } catch (error) {
            formRef.current?.setErrors(getValidationErrors(error));
        }
    }, [addToast, amplifyTempPwd, intl, successToastMessageDescription, successToastMessageTitle, user.email]);

    return (
        <>
            <HeaderLogin isModal={isModal} previousStep="ValidationCode" onCloseClick={onCloseClick} />
            <div className={classes.root}>
                <Title>
                    {translate({ id: "ChangePassword.title" })}
                </Title>
                <SubTitle className={classes.subTitle}>
                    {translate({
                        id: "ChangePassword.subTitle",
                    })}
                </SubTitle>
                <Form ref={formRef} handleSubmit={handleSubmit} >
                    <Input
                        type="password"
                        name="password"
                        placeholderText={translate({ id: "ChangePassword.passwordPlaceHolder" })}
                        showTip
                    />
                    <Input
                        type="password"
                        name="confirmPassword"
                        placeholderText={translate({ id: "ChangePassword.confirPassword" })}
                        showTip
                    />
                    <InternalButton type="submit" text={intl.formatMessage({ id: "ChangePassword.actionButton" })} />
                </Form>

            </div>
            <Terms />
        </>
    );
};


export default injectIntl(ChangePassword);
