import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { Filter } from "@cvccorp-components/icons";
import Theme from "../../Theme";
import themes from "../../Theme/themes";
import Button from "../Button";
import Badge from "../../Badge";
import { BrandType } from "../../types/brand";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        padding: "20px",
    },
}));

type IconsButtonsProps = {
    label: string,
    mode: string,
    size: string,
    brand: BrandType,
    color: string,
    variant: string,
    iconPosition: string,
}

export const IconButtons = ({ brand, mode, label, color, variant, size, iconPosition }: IconsButtonsProps): JSX.Element => {
    const classes = useStyles(themes[brand]);
    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="botoes">
                <Typography variant="h3">Botões de Ícones</Typography>
                <br />
                <Divider />
                <br />
                <Grid container direction="column" justify="center" spacing={2} alignItems="flex-start">
                    <Grid item xs={12}>
                        <Typography variant="subtitle1">Botões com Ícone Default com Notificação</Typography>
                        <br />
                        <Button
                            color={color}
                            label={label}
                            icon={<Filter />}
                            variant={variant}
                            size={size}
                            iconPosition={iconPosition}
                        />
                        <Badge content={10}>
                            <Button
                                color={color}
                                label={label}
                                icon={<Filter />}
                                variant={variant}
                                size={size}
                                iconPosition={iconPosition}
                            />
                        </Badge>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="subtitle1">Botão Agrupado</Typography>
                        <br />
                        <Button
                            color={color}
                            label={label}
                            icon={<Filter />}
                            iconPosition={iconPosition}
                            grouped={1}
                            variant={variant}
                            size={size}
                        />
                        <Button
                            color={color}
                            label={label}
                            icon={<Filter />}
                            iconPosition={iconPosition}
                            grouped={2}
                            variant={variant}
                            size={size}
                        />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        label: 'Button Text',
        brand: "CVC",
        mode: "root",
        color: 'primary',
        variant: 'contained',
        iconPosition: 'before',
        size: 'medium'
    },
    argTypes: {
        label: { type: 'string' },
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"],
            brand: "CVC",
            mode: "root",
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        color: {
            options: ['default', 'primary', 'secondary'],
            control: { type: 'select' }
        },
        iconPosition: {
            options: ['before', 'after'],
            control: { type: 'radio' }
        },
        variant: {
            options: ['outlined', 'contained'],
            control: { type: 'radio' }
        },
        size: {
            options: ['small', 'medium', 'large'],
            control: { type: 'select' }
        }
    }
}

storiesOf("Components/Buttons/Button", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Ícones", (args) => <IconButtons {...args}/>, argsControls);
