import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import FilterOption from "../FilterOption/FilterOption";
import FilterText from "../FilterText/FilterText";
import FilterGrid from "../FilterGrid/FilterGrid";
import FilterRange from "../FilterEngine/_FilterRange";
import Filter from "../Filter";
import Filters from "../Constants";

const uuidv4 = require("uuid/v4");

const FilterMore = ({
    isOpen,
    filters,
    onApply,
    isMobile,
    label,
    noModal,
    header,
    className,
    onActives,
    height,
    item,
}) => {
    const [valueOpen, setValueOpen] = useState(false);
    const [items, setItems] = useState([]);

    useEffect(() => {
        setValueOpen(isOpen);
    }, [isOpen]);

    useEffect(() => {
        let fts = [...filters];

        fts = fts.map(ft => {
            if (ft.type === "range" && !ft.rangeValue) {
                Object.assign(ft, { rangeTitleValues: { min: ft.min, max: ft.max } });
                Object.assign(ft, { rangeValue: { min: ft.min, max: ft.max } });
                Object.assign(ft, { rangeTextValues: { min: ft.min, max: ft.max } });
                Object.assign(ft, { open: false });
            }
            return ft;
        });

        setItems([...fts]);
    }, [filters]);

    const filterActives = () => items.filter(filter => filter.isActive);

    const cleanFilter = () => {
        items.forEach((filter, index) => {
            if (filter.type === Filters.RANGE) {
                items[index].rangeValue = {
                    min: items[index].min,
                    max: items[index].max,
                };
                items[index].rangeTextValues = {
                    min: items[index].min,
                    max: items[index].max,
                };
                items[index].isActive = false;
            }
            if (filter.type === Filters.OPTIONS) {
                filter.options.forEach(option => {
                    Object.assign(option, { checked: false });
                });
                items[index].isActive = false;
            }

            if (filter.type === Filters.GRID) {
                filter.options.forEach(option => {
                    Object.assign(option, { checked: false });
                });
                items[index].isActive = false;
            }

            if (filter.type === Filters.TEXT) {
                Object.assign(filter, { value: "" });
                items[index].isActive = false;
            }
        });

        setItems([...items]);
    };

    const handlerReset = () => {
        cleanFilter();
        onApply(items);
        onActives(filterActives().length);
    };



    const handerFilters = () => {
        const it = items.map((item, index) => {
            if (item.type === Filters.RANGE) {
                return (
                    <FilterRange
                        key={uuidv4()}
                        onChange={_item => {
                            items[index] = _item;
                            setItems([...items]);
                        }}
                        values={{ min: item.rangeValue.min, max: item.rangeValue.max }}
                        onActive={isActive => {
                            items[index].isActive = isActive;
                            setItems([...items]);
                        }}
                        noModal
                        isMobile={isMobile}
                        index={index}
                        filters={items}
                        filter={item}
                    />
                );
            }
            if (item.type === Filters.OPTIONS) {
                return (
                    <FilterOption
                        key={uuidv4()}
                        isMobile={isMobile}
                        noModal
                        onChange={(itemChange, options) => {
                            Object.assign(items[index].options, options);
                            setItems([...items]);
                        }}
                        onActive={isActive => {
                            items[index].isActive = isActive;
                            setItems([...items]);
                        }}
                        items={item.options}
                        label={item.label}
                    />
                );
            }
            if (item.type === Filters.GRID) {
                return (
                    <FilterGrid
                        isMobile={isMobile}
                        noModal
                        label={item.label}
                        isAmenities
                        items={item.options}
                        onChange={(itemChange, options) => {
                            Object.assign(items[index].options, options);
                            setItems([...items]);
                        }}
                        onActive={isActive => {
                            items[index].isActive = isActive;
                            setItems([...items]);
                        }}
                    />
                );
            }
            if (item.type === Filters.TEXT) {
                return (
                    <FilterText
                        onChange={value => {
                            items[index].value = value;
                            setItems([...items]);
                        }}
                        onActive={isActive => {
                            items[index].isActive = isActive;
                            setItems([...items]);
                        }}
                        value={item.value}
                        label={item.label}
                        noModal
                    />
                );
            }
            return [];
        });
        return it;
    };

    const handleApply = () => {
        onApply(items);
        onActives(filterActives().length);
        setValueOpen(false);
    };

    // const value =
    //   item.filterTitle && item.filterTitle.length > 0 ? item.filterTitle : [""];
    // const isActive =
    //   isMobile || !item.filterTitle ? false : filterActives().length > 0;

    const handleClose = () => {
        if (!isMobile) setValueOpen(false);
        if (!item.isActive) cleanFilter();
    };

    return (
        <Filter
            type="list"
            more
            isMobile={isMobile}
            label={label}
            noModal={noModal}
            onOpen={() => !isMobile && setValueOpen(true)}
            isOpen={valueOpen}
            onClose={handleClose}
            onApply={() => handleApply()}
            items={handerFilters()}
            onReset={() => handlerReset()}
            isActive={item.isActive}
            sufix="- Mais Filtros"
            value={item.value}
            qtdFilters={filterActives().length}
            // value={filterActives().length === 1 ? [1] : filterActives()}
            disabled={!filterActives().length > 0}
            header={header}
            className={className}
            height={height}
        />
    );
};

FilterMore.defaultProps = {
    onApply: () => { },
    onActives: () => { },
    isOpen: false,
    isMobile: false,
    label: "",
    noModal: false,
    header: "",
    filters: [],
};

FilterMore.propTypes = {
    isOpen: PropTypes.bool,
    filters: PropTypes.arrayOf(
        PropTypes.shape({
            type: PropTypes.string,
            label: PropTypes.string,
            field: PropTypes.string,
        })
    ),
    onActives: PropTypes.func,
    onApply: PropTypes.func,
    isMobile: PropTypes.bool,
    label: PropTypes.string,
    noModal: PropTypes.bool,
    header: PropTypes.string,
};

export default FilterMore;
