/* eslint-disable no-restricted-syntax */
/* eslint-disable prefer-rest-params */
import * as React from "react"

if (typeof window !== "undefined") {
    const originalSetItem = localStorage.setItem
    // eslint-disable-next-line func-names
    localStorage.setItem = function (key: string, value: string) {
        if (key.startsWith("CognitoIdentityServiceProvider") && key.endsWith("idToken")) {
            const event = new CustomEvent("setCognitoIdToken", {
                "detail": { "key": key, "value": value }
            })
            document.dispatchEvent(event)

        }
        originalSetItem.apply(this, [key, value])
    }

    const originalRemoveItem =  localStorage.removeItem

    // eslint-disable-next-line func-names
    localStorage.removeItem = function (key: string) {
        if (key.startsWith("CognitoIdentityServiceProvider") && key.endsWith("idToken")) {
            const event = new Event("removeCognitoIdToken")
            document.dispatchEvent(event)
        }
        originalRemoveItem.apply(this, [key])
    }
}
// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
function useLocalStorage() {

    const [cognitoIdToken, setIdToken] = React.useState<string | null | undefined>()

    const getCognitoIdToken = (e: Event) => {
        const customEvent: CustomEvent = e as CustomEvent;
        setIdToken(customEvent.detail.value);
    }

    const deleteCognitoServiceProvider = () => {
        // eslint-disable-next-line vars-on-top
        for (var key of Object.keys(localStorage)) {
            if (key.startsWith("CognitoIdentityServiceProvider")) {
                localStorage.removeItem(key)
            }
        }
    }

    const removeCognitoIdToken = () => {
        setIdToken(undefined);

    }

    React.useEffect(() => {
        document.addEventListener("setCognitoIdToken",
            getCognitoIdToken,
            false)

        document.addEventListener("removeCognitoIdToken",
            removeCognitoIdToken,
            false)

        return () => document.removeEventListener("storageChange", getCognitoIdToken)
    }, [])

    return { cognitoIdToken, deleteCognitoServiceProvider }
}

export default useLocalStorage
