import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import Theme from "../../Theme";
import themes from "../../Theme/themes";
import Button from "../Button";
import { BrandType } from "../../types/brand";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        padding: "20px",
    },
}));

type TypeButtonsProps = {
    label: string,
    mode: string,
    size: string,
    brand: BrandType,
    color: string,
    variant: string,
    disabled: boolean,
    fullWidth: boolean,
}

export const TypeButtons = ({ label, mode, size, brand, color, variant, disabled, fullWidth }: TypeButtonsProps): JSX.Element => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="botoes">
                <Typography variant="h3">Tipos e Cores</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={3}>
                    <Button fullWidth={fullWidth} color={color} label={label} variant={variant} size={size} disabled={disabled} />
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        label: 'Button Text',
        brand: "CVC",
        mode: "root",
        size: 'medium',
        color: 'primary',
        variant: 'contained',
        disabled: false,
        fullWidth: false,
    },
    argTypes: {
        label: { type: 'string' },
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        size: {
            options: ['small', 'medium', 'large'],
            control: { type: 'select' }
        },
        color: {
            options: ['default', 'primary', 'secondary'],
            control: { type: 'select' }
        },
        variant: {
            options: ['outlined', 'contained'],
            control: { type: 'radio' }
        },
        disabled: { type: 'boolean' },
        fullWidth: { type: 'boolean' }
    }
}

storiesOf("Components/Buttons/Button", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default", (args) => <TypeButtons {...args}/>, argsControls);
