import React from "react";
import { Paper, Divider, Typography, Grid } from "@material-ui/core";
import { jsxDecorator } from "storybook-addon-jsx";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { Hotel } from "@cvccorp-components/icons";
import Theme from "../Theme";
import Pin from "../Pin";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
}));

const LocationIcons = () => {
    const classes = useStyles();

    return (
        <Paper className={classes.root} id="botoes">
            <Typography variant="h3">Ícones de Localização</Typography>
            <br />
            <Divider />
            <br />
            <Grid container direction="row" justify="flex-start" spacing={2}>
                <Grid item xs={1}>
                    <Pin color="light">30</Pin>
                </Grid>
                <Grid item xs={1}>
                    <Pin color="light">R$ 198</Pin>
                </Grid>
                <Grid item xs={1}>
                    <Pin color="dark">30</Pin>
                </Grid>
                <Grid item xs={1}>
                    <Pin color="dark">
                        <Hotel noGutter size="small" />
                    </Pin>
                </Grid>
                <Grid item xs={1}>
                    <Pin color="dark">30</Pin>
                </Grid>
            </Grid>
        </Paper>
    );
};

storiesOf("Components/Icons", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Ícones de Localização", () => <LocationIcons />);
