import React, { useEffect, useState, memo } from "react";
import { ToastMessage, useToast } from "../../../../contexts/Toast";
import useStyles from "./styles";
import CloseButtonIcon from "../../icons/CloseButtonIcon";
import Snackbar from "@material-ui/core/Snackbar";
import Slide, { SlideProps } from "@material-ui/core/Slide";
import withWidth, { isWidthDown, WithWidth } from "@material-ui/core/withWidth";
interface ToastProps extends WithWidth{
    message: ToastMessage;
    key: string;
}

type TransitionProps = Omit<SlideProps, "direction">;

const TransitionLeft = (props: TransitionProps) => {
    return <Slide {...props} direction="left" />;
};

const Toast: React.FC<ToastProps> = ({ message, key , width}: ToastProps) => {
    const { removeToast } = useToast();
    const [open, setOpen] = useState(false);
    const isMobile = isWidthDown("sm",width);
    const [transition, setTransition] = useState<React.ComponentType<TransitionProps> | undefined>(undefined);

    useEffect(() => {
        if (message) {
            setTransition(() => TransitionLeft);
            setOpen(true);
            const timer = setTimeout(() => {
                removeToast(message.id);
                setOpen(false);
            }, 6000);

            return () => {
                clearTimeout(timer);
            };
        }
    }, [removeToast, message.id]);

    const classes = useStyles({type: message.type || "success", isMobile:isMobile});

    return (
        <Snackbar
            key={key}
            open={open}
            TransitionComponent={transition}
            autoHideDuration={6000}
        >
            <div className={classes.container}>
                <div className={classes.toastWrapper}>
                    <strong>{message.title}</strong>
                    {message.description && <p className={classes.description}> {message.description}</p>}
                </div>

                <button
                    className={classes.closeButton}
                    onClick={() => removeToast(message.id)}
                    type="button">
                    <CloseButtonIcon />
                </button>
            </div>
        </Snackbar>
    );
};

export default (withWidth)()(memo(Toast));
