/* eslint-disable @typescript-eslint/ban-types */
import React from "react";
import { Filter } from "@cvccorp-components/icons";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import CustomChip from "../CustomChip";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
  ...styles,
  root: {
    background: theme.palette.background.default,
    boxShadow: theme.shadows[8],
    padding: "30px",
  },
  darkBg: {
    background: "rgba(0, 0, 0, 0.6)",
    boxShadow: theme.shadows[8],
    padding: "30px",
  },
}));

interface DefaultCustomChip {
    mode: string
    label: string
    brand: BrandType
    disabled: boolean
    variant: "outlined" | "contained"
    color: "primary" | "secondary"
    className: string
}

const DefaultCustomChip = ({
  mode, brand, variant, color, className, disabled, label,
}: DefaultCustomChip) => {
  const classes = useStyles(themes[brand]);

  return (
    <MuiThemeProvider theme={themes[brand]}>
      <Paper classes={{ root: classes[mode] }} id="chips">
        <Typography variant="h3">Chips</Typography>
        <br />
        <Divider />
        <br />

        <Grid container direction="row" alignItems="flex-start" spacing={2}>
          <Grid item xs={2}>
            <CustomChip
              label={label}
              disabled={disabled}
              variant={variant}
              color={color}
              className={className}
              icon={<Filter />}
            />
          </Grid>
          <Grid item xs={2}>
            <CustomChip
              label={label}
              disabled={disabled}
              variant={variant}
              color={color}
              className={className}
            />
          </Grid>
        </Grid>
      </Paper>
    </MuiThemeProvider>

  );
};

const argsControls = {
  args: {
    brand: "CVC",
    label: "Chip",
    mode: "root",
    disabled:  false,
    variant: "contained",
    color: "primary",
  },
  argTypes: {
    brand: {
      type: "select",
      options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
    },
    label: { type: "string" },
    mode: {
      options: ["root", "darkBg"],
      control: { type: "radio" }
    },
    variant: {
      options: ["outlined", "contained"],
      control: { type: "select" }
    },
    color: {
      options: ["primary", "secondary"],
      control: { type: "radio" }
    },
  }
}

storiesOf("Components/Buttons/CustomChip", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
  .add("Default", (args) => <DefaultCustomChip {...args}/>, argsControls);
