import React from "react";
import { Paper, Typography, Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { jsxDecorator } from "storybook-addon-jsx";
import { muiTheme } from "storybook-addon-material-ui";
import Theme from "../Theme";
import * as Themes from "../Theme/themes";
import { StyledPre, themeOverWrites } from "./ColorsGuide.stories.styles";

const useStyles = makeStyles(themeOverWrites);

const getThemePalleteObject = theme => Themes.default[theme].palette;
const pretiffyData = obj => JSON.stringify(obj, null, 2);

const Section = ({ sectionKey, brand }) => {
    const capitalize = str => str.charAt(0).toUpperCase() + str.slice(1);
    const content = pretiffyData(getThemePalleteObject(brand)[sectionKey]);

    return content ? (
        <>
            <Typography variant="subtitle1">{capitalize(sectionKey)}</Typography>
            <StyledPre>{`${sectionKey}: ${content}`}</StyledPre>
        </>
    ) : null;
};

const ColorsGuide = ({ brand }) => {
    const classes = useStyles();

    const paletteKeys = Object.keys(getThemePalleteObject(brand));

    return (
        <Paper className={classes.root} id="chips">
            <Typography variant="h3">Cores - {brand}</Typography>
            <br />
            <Divider />
            <br />
            {paletteKeys.map(palette => {
                return <Section brand={brand} key={palette} sectionKey={palette} />;
            })}
            <Typography variant="h3">Objeto Completo - {brand}</Typography>
            <StyledPre>{pretiffyData(getThemePalleteObject(brand))}</StyledPre>
        </Paper>
    );
};

const argsOptions = {
    args: {
        brand: "CVC"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        }
    }
};

storiesOf("Theme/Colors", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Guia de cores", args => <ColorsGuide {...args} />, argsOptions);
