import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import OrderEngine from "../OrderEngine";
import dbAir from "./dbAir.json";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

const orders = [
    {
        value: "Menor valor",
        field: "total",
        operation: "asc"
    },
    {
        value: "Maior valor",
        field: "total",
        operation: "desc"
    },
    {
        value: "Mais rápido",
        field: item => {
            const totalMinutesGo = item.segments[0].totalMinuteDuration;
            const totalMinutesBack = (item.segments.find(s => s.segmentIndex === 1) || {}).totalMinuteDuration;
            return [totalMinutesGo, totalMinutesBack, item.total];
        },
        operation: ["asc", "asc", "desc"]
    },
]

type OrderEngineStoryProps = {
    mode: string,
    brand: BrandType
}

const OrderEngineStory = ({
    mode, brand
}: OrderEngineStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="OrderEngine">
                <Typography variant="h3">OrderEngine</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <OrderEngine
                        orders={orders}
                        dataSource={dbAir.priceGroups}
                        onOrdered={() => undefined}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
    }
}

storiesOf("Components/OrderEngine", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <OrderEngineStory {...args}/>, argsControls);
