import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import themes from "../../Theme/themes";
import ThemeProviderCustom from "../../ThemeProviderCustom";
import { BrandType } from "../../types/brand";
import mockBrands from "../../utils/brandMock";
import Rate from "../Rate";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RateStoryProps = {
    mode: string,
    brand: BrandType,
    rate: number
}

const RateStory = ({
    mode, brand, rate
}: RateStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <BrandContext.Provider value={mockBrands[brand]}>
            <ThemeProviderCustom>
                <Paper className={classes[mode]} id="Rate">
                    <Typography variant="h3">Rate</Typography>
                    <br />
                    <Divider />
                    <br />
                    <Grid item xs={2}>
                        <Rate rate={rate} />
                    </Grid>
                </Paper>
            </ThemeProviderCustom>
        </BrandContext.Provider>
    );
};

const argsControls = {
    args: {
        brand: "cvc",
        mode: "root",
        rate: 3
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        rate: {
            type: "number"
        }
    }
}

storiesOf("Components/Rate", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <RateStory {...args}/>, argsControls);
