import crypto from "crypto";
import React, { createContext, useContext, useState } from "react";
import { BrandContext } from "../../BrandContext";

interface IAuthAmplifyContext {
    authAmplify: string;
    setAuthAmplify(isAuthAmplify: string): void;
    amplifyTempPwd: string;
    amplifyPwd?: string;
    setAmplifyPwd(pwd: string): void;
}

const AuthAmplifyContext = createContext<IAuthAmplifyContext>({} as IAuthAmplifyContext);

const AuthAmplifyProvider: React.FC = ({ children }) => {
    const [authAmplify, setAuthAmplify] = useState<string>("");
    const [amplifyPwd, setAmplifyPwd] = useState<string>("");
    const temp = crypto.randomBytes(10).toString("hex");
    const ctx = useContext(BrandContext);
    const amplifyTempPwd = (`${temp}@${ctx.brand}`);
    return (
        <AuthAmplifyContext.Provider value={{ authAmplify, setAuthAmplify, amplifyTempPwd, amplifyPwd, setAmplifyPwd }}>
            {children}
        </AuthAmplifyContext.Provider>
    );
};

function useAuthAmplifyContext(): IAuthAmplifyContext {
    const context = useContext(AuthAmplifyContext);
    return context;
}

export { AuthAmplifyProvider, useAuthAmplifyContext };
