export enum IProviders {
    COGNITO = "cognito",
    LEGACY = "legacy",
    FACEBOOK = "facebook",
    APPLE = "apple",
    GOOGLE = "google",
    CORP = "corp"
}
export interface IUserExists {
    emailConfirmed: boolean;
    isSocial: boolean;
    provider: IProviders;
    nextStep?: string;
}

export function usersInProvider(usersExists: IUserExists[], amICorp = false): IUserExists | undefined {
    if (!amICorp) {
        const corpUser = updateUserNextStep(usersExists, [IProviders.CORP], "ProfileCorp");
        if (corpUser) {
            return corpUser;
        }
    }

    let user = updateUserNextStep(usersExists, [IProviders.COGNITO, IProviders.LEGACY], "Password");
    if (!user) {
        user = updateUserNextStep(usersExists, [IProviders.FACEBOOK, IProviders.GOOGLE, IProviders.APPLE], "SocialLoginAccount");
    } else {
        user.nextStep = user.emailConfirmed ? user.nextStep : "UnverifiedAccount";
    }
    return user;
}

function updateUserNextStep(usersExists: IUserExists[], providerArray: IProviders[], nextStep: string): IUserExists | undefined {
    const user = filterUserByProviders(usersExists, providerArray);
    if (user) {
        user.nextStep = nextStep;
        return user;
    }
}

function filterUserByProviders(users: IUserExists[], providers: IProviders[]): IUserExists {
    const usersFilter = users
        .filter((user: IUserExists) => {
            return providers.find(el => el == user.provider);
        })
        .sort((a, b) => (providers.indexOf(a.provider) > providers.indexOf(b.provider) ? 1 : -1));
    return usersFilter[0];
}
