import React from "react";
import { Paper, Divider, Typography, Grid } from "@material-ui/core";
import { jsxDecorator } from "storybook-addon-jsx";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { Luggage } from "@cvccorp-components/icons";
import Theme from "../Theme";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
}));

const BaggageIcons = () => {
    const classes = useStyles();

    return (
        <Paper className={classes.root} id="botoes">
            <Typography variant="h3">Ícones de Bagagem</Typography>
            <br />
            <Divider />
            <br />
            <Grid container direction="row" justify="flex-start" spacing={2}>
                <Grid item xs={1}>
                    <Luggage size="medium" />
                    <Typography variant="subtitle1">Luggage</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Luggage size="medium" carryOn />
                    <Typography variant="subtitle1">Luggage</Typography>
                </Grid>
            </Grid>
        </Paper>
    );
};

storiesOf("Components/Icons", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Ícones de Bagagem", () => <BaggageIcons />);
