import React, { useRef, useContext, useCallback, useEffect } from "react";
import { Typography } from "@material-ui/core";
import { Auth } from "aws-amplify";
import { injectIntl, IntlShape } from "react-intl";
import useGa from "../../../utils/useGa"
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import { useLoadingContext } from "../../contexts/LoadingContext";
import { useRenderSteps } from "../../contexts/renderSteps";
import HeaderLogin from "../../shared/components/HeaderLogin";
import InternalButton from "../../shared/components/InternalButton";
import Loading from "../../shared/components/Loading";
import SubTitle from "../../shared/components/SubTitle";
import Terms from "../../shared/components/Terms";
import Title from "../../shared/components/Title";
import { useUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import useStyles from "./styles";

interface SubmitFormData {
    email: string;
    name?: string;
    lastName?: string;
    userExists: boolean;
}

interface IInternalLogin {
    intl: IntlShape;
    onCloseClick?(): void;
    isModal?: boolean;
}

const UnverifiedAccount: React.FC<IInternalLogin> = ({ intl, onCloseClick, isModal }) => {
    const { user } = useUserData();
    const { renderNewStep, setPreviousStep } = useRenderSteps();
    const { loading } = useLoadingContext();
    const { saveOnGa } = useGa();
    const { authAmplify } = useAuthAmplifyContext();

    const classes = useStyles({});

    useEffect(() => {
        setPreviousStep("MainScreen");
    }, []);

    const handleClick = async () => {
        try {
            if (authAmplify == "confirmSignUp") {
                await Auth.resendSignUp(user.email);
                renderNewStep("ValidationCode");
                saveOnGa({
                    event: "login_event",
                    category: "Account",
                    action: "Registration",
                    label: "alert :: verify email"
                });
                renderNewStep("ValidationCode")
            } else {
                await Auth.forgotPassword(user.email);
                renderNewStep("ValidationCode");
            }
        }catch(err) {
            // handle error server
        }
        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "Registration",
            label: "alert :: verify email"
        });
        renderNewStep("ValidationCode")
    }

    return (
        loading ? <Loading /> :
            <>
            <HeaderLogin isModal={isModal} previousStep="MainScreen" onCloseClick={onCloseClick} />
            <div className={classes.root}>
                    <Title>
                    {translate({
                            id: "UnverifiedAccount.title",
                        })}
                </Title>
                    <SubTitle>
                    {translate({
                            id: "UnverifiedAccount.subTitle",
                            value: {
                                email: <strong>{user ? user.email : ""}</strong>

                            },
                        })}
                </SubTitle>
                    <Typography variant="body1" style={{ fontWeight: "bold", marginBottom: 7, fontSize: 14 }}>
                    {translate({
                            id: "UnverifiedAccount.verifyNow",
                        })}
                </Typography>

                    <InternalButton handlerClick={handleClick} type="submit" text={intl.formatMessage({ id: "UnverifiedAccount.textBtn" })} />

                </div>
            <Terms />
        </>
    );
};

export default injectIntl(UnverifiedAccount);
