/* eslint-disable @typescript-eslint/no-empty-function */
import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import Price from "../Price";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RoomPriceStoryProps = {
    mode: string,
    brand: BrandType,
    days: string,
    partialPrice: string,
    parcel: string ,
    totalPrice: string,
    oldPartialPrice: string,
    label: string
}

const RoomPriceStory = ({
    mode, 
    brand,
    days,
    partialPrice,
    parcel,
    totalPrice,
    oldPartialPrice,
    label
}: RoomPriceStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Room/Price">
                <Typography variant="h3">RoomPrice</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <Price 
                        onClick={() => {}}
                        product="hotel"
                        taxesIncluded 
                        symbol={undefined} 
                        days={days}
                        partialPrice={partialPrice}
                        parcel={parcel}
                        totalPrice={totalPrice}
                        oldPartialPrice={oldPartialPrice}
                        label={label}
                        variant={undefined} 
                        classButton={undefined} 
                        totalPeople={undefined}                    
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        days: "2",
        partialPrice: "100",
        parcel: "12" ,
        totalPrice: "200",
        oldPartialPrice: "200",
        label: "Texto Label"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        days: { type: "string" },
        partialPrice: { type: "string" },
        parcel: { type: "string" },
        totalPrice: { type: "string" },
        oldPartialPrice: { type: "string" },
        label: { type: "string" },
    }
}

storiesOf("Components/Room/Price", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <RoomPriceStory {...args}/>, argsControls);
