import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import CustomTypography from "../Typography";
import { styles } from "./styles";

function TabContainer({ children }) {
    return (
        <div>
            <CustomTypography component="body1">{children}</CustomTypography>
        </div>
    );
}
TabContainer.propTypes = { children: PropTypes.node.isRequired };

const CustomTab = ({ classes, content, selected, handleChange }) => {
    const [value, setValue] = React.useState(selected || content[0].value);

    function onHandleChange(event, newValue) {
        if (handleChange) handleChange(newValue && newValue);
        setValue(newValue);
    }

    return (
        <div>
            <Tabs value={value} onChange={onHandleChange} className={classes.container} TabIndicatorProps={{ style: { bottom: "5px" } }}>
                {content.map((item, key) => (
                    <Tab
                        classes={{
                            root: classes.root,
                            label: classes.label,
                            textColorInherit: classes.textColorInherit
                        }}
                        value={item.value}
                        key={key}
                        label={item.label}
                        selected={item.value === value}
                    />
                ))}
            </Tabs>
            {content.map((item, key) => {
                if (value === item.value) {
                    return <TabContainer key={key}>{item.content}</TabContainer>;
                }
                return null;
            })}
        </div>
    );
};

CustomTab.propTypes = {
    classes: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    content: PropTypes.oneOfType([PropTypes.array, PropTypes.string]),
    handleChange: PropTypes.func
};

export default withStyles(styles, { withTheme: true })(CustomTab);
