import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Filter from "../Filter";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type FilterStoryProps = {
    mode: string,
    brand: BrandType,
    label: string,
    isOpen: boolean,
    isMobile: boolean,
    disabled: boolean,
}

const Options: unknown[] = [<p key="1">Opção 1</p>, <p key="2">Opção 2</p>]

const FilterStory = ({
    mode, brand, label, isOpen, isMobile, disabled
}: FilterStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Filter">
                <Typography variant="h3">Filter</Typography>
                <br />
                <Divider />
                <br />
                <Grid xs={4}>
                    <Filter 
                        classes={classes[mode]}
                        label={label}
                        items={Options} 
                        header={false} 
                        disabled={disabled} 
                        more={false} 
                        isOpen={isOpen}
                        isMobile={isMobile}
                        qtdFilters={2}                    
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        label: "Valor da passagem",
        isOpen: false,
        isMobile: false,
        disabled: false,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        label: {
            type: "string"
        },
        isOpen: { type: "boolean" },
        isMobile: { type: "boolean" },
        disabled: { type: "boolean" }
    }
}

storiesOf("Components/Filtros/Filter", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <FilterStory {...args}/>, argsControls);
