import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import FilterText from "../FilterText";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type FilterTextStoryProps = {
    mode: string,
    brand: BrandType,
    label: string,
    open: boolean,
    value: string
}

const FilterTextStory = ({
    mode, brand, label, open, value
}: FilterTextStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="FilterText">
                <Typography variant="h3">FilterText</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <FilterText 
                        label={label} 
                        open={open} 
                        value={value} 
                        height
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        open: false,
        label: "Label Text",
        value: ""
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        open: { type: "boolean" },
        label: { type: "string" },
        value: { type: "string" }
    }
}

storiesOf("Components/Filtros/FilterText", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <FilterTextStory {...args}/>, argsControls);
