import React from "react";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import CircularLoader from "../CircularLoader";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type LoadingsProps = {
    brand: BrandType,
    mode: string,
    color: string,
}

export const CircularLoaderComponent = ({ brand, mode, color }: LoadingsProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="loading">
                <Typography variant="h3">Circular Loader</Typography>
                <br />
                <Divider />
                <br />

                <Grid container spacing={2}>
                    <Grid item xs={12} lg={6}>
                        <CircularLoader className="" color={color} />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        color: "secondary"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        color: {
            type: "select",
            options: ["primary", "secondary"]
        },
    }
};

storiesOf("Components/Loadings/CircularLoader", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <CircularLoaderComponent {...args} />, argsControls);
