import React, { createContext, ReactNode, useCallback, useContext, useEffect, useState } from "react";
import { BrandContext } from "../BrandContext"
import { Redirect } from "../BrandContext/amplify/aws.config"
import api from "../BrandContext/services/api";
import { useToast } from "../MyTravelLogin/contexts/Toast";
import { useUserDataContext } from "../UserContext";
import { findCognitoIdToken } from "../utils";
import { IPatchProfile, IProfileContext, IProfileData } from "./types";

const ProfileContext = createContext<IProfileContext>({} as IProfileContext);

interface IProfileProvider {
    children: ReactNode;
}

const ProfileDataProvider: React.FC<IProfileProvider> = ({ children }: IProfileProvider) => {
    const ctx = useContext(BrandContext);
    const {userIdToken} = useUserDataContext()
    const [profileData, setProfileData] = useState<IProfileData>({} as IProfileData);
    const [profileLoading, setProfileLoading] = useState(false)
    const [isPatchProfileScreen, setIsPatchProfileScreen] = useState(false)
    const [isRegisterUserProfileStep, setIsRegisterUserProfileStep] = useState(false)

    const getProfileInformations = useCallback(async () => {
        setProfileLoading(true)
        try {
            const token = userIdToken || findCognitoIdToken()
            if(token){
                api.defaults.headers.authorization = `Bearer ${token}`;
                const profile =  await api.get(`${Redirect(ctx.hosts)}/api/my-travels-bff/profile`)
                if (profile.data.userId && profile.data.userId.length > 0) {
                    if((window as any) && (window as any).fingerprint) {
                        (window as any).fingerprint.storeUserId(profile.data.userId);
                    }
                }
                setProfileData(profile?.data)
                setProfileLoading(false)
                return profile?.data
            }
        } catch {
            // precisamos tratar essa
            setProfileLoading(false)
        }

    }, [ctx.hosts, userIdToken]);

    const patchProfile = useCallback(async({birthdate, contact, cpf, name, acceptedMaisCVC}: IPatchProfile) => {
        setProfileLoading(true)
        const data = {
            name,
            birthdate,
            cpf,
            contact,
            acceptedMaisCVC
        }
        try {
            api.defaults.headers.authorization = `Bearer ${userIdToken}`;
            await api.patch(`${Redirect(ctx.hosts)}/api/my-travels-bff/profile`, data)
            await getProfileInformations()
            setProfileLoading(false)
        } catch (err){
            console.log("Erro: ", err) // Tratar esse erro com o Mauricio amanha
            setProfileLoading(false)
        }

    }, [userIdToken, getProfileInformations])

    const setIsPatchProfileScreenToggle = useCallback((isProfileScreen: boolean) => {
        setIsPatchProfileScreen(isProfileScreen)
    }, [])

    const setIsRegisterUserProfileStepToggle = useCallback((isRegisterProfile: boolean)=> {
        setIsRegisterUserProfileStep(isRegisterProfile)
    }, [])

    return (
        <ProfileContext.Provider value={{
            profileData,
            getProfileInformations,
            patchProfile,
            profileLoading,
            isPatchProfileScreen,
            setIsPatchProfileScreenToggle,
            isRegisterUserProfileStep,
            setIsRegisterUserProfileStepToggle,
            setProfileData
        }}>
            {children}
        </ProfileContext.Provider>
    );
};

function useProfileContext(): IProfileContext {
    const context = useContext(ProfileContext);
    return context;
}

export { ProfileDataProvider, useProfileContext };
