/* eslint-disable no-empty */
import React, { useRef, useCallback, useEffect, useState, useMemo, useContext } from "react";
import Link from "@material-ui/core/Link";
import { FormHandles } from "@unform/core";
import { Auth } from "aws-amplify";
import { IntlShape, injectIntl } from "react-intl";
import * as Yup from "yup";
import { BrandContext } from "../../../BrandContext";
import { Redirect } from "../../../BrandContext/amplify/aws.config"
import {useProfileContext} from "../../../ProfileContext"
import { useUserDataContext } from "../../../UserContext";
import { getParam , featureToggles, verifyIsBrand } from "../../../utils";
import useGa from "../../../utils/useGa";
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import { useLoadingContext } from "../../contexts/LoadingContext";
import { useRenderSteps } from "../../contexts/renderSteps";
import { useToast } from "../../contexts/Toast";
import Form from "../../shared/components/Form";
import HeaderLogin from "../../shared/components/HeaderLogin";
import Input from "../../shared/components/Input";
import InternalButton from "../../shared/components/InternalButton";
import Loading from "../../shared/components/Loading";
import SubTitle from "../../shared/components/SubTitle";
import Terms from "../../shared/components/Terms";
import Title from "../../shared/components/Title";
import { useUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import useStyles from "./styles";

interface IPasswordProps {
    password: string;
}

interface TPasswordProps {
    onCloseClick?(): void;
    intl: IntlShape;
    isModal?: boolean;
}

interface ISignIn {
    email: string
    password:string
}

const Password: React.FC<TPasswordProps> = ({ intl, onCloseClick, isModal }) => {
    const classes = useStyles({});
    const formRef = useRef<FormHandles>(null);
    const { user, setUser } = useUserData();
    const { renderNewStep, setPreviousStep } = useRenderSteps();
    const ctx = useContext(BrandContext);
    const linkChangeEmail = intl.formatMessage({ id: "Password.linkChangeEmail" });
    const errorPasswordRequired = intl.formatMessage({ id: "Password.errorPasswordRequired" });
    const errorMessage = intl.formatMessage({ id: "Password.errorMessage" });
    const successToastMessageTitle = intl.formatMessage({ id: "Password.successToastMessageTitle" });
    const successToastMessageDescription = intl.formatMessage({ id: "Password.successToastMessageDescription" });
    const { loading, setLoading } = useLoadingContext();
    const { setAmplifyPwd } = useAuthAmplifyContext();
    const { addToast } = useToast();
    const { saveOnGa } = useGa();
    const [hasDoneProfileReq , setHasDoneProfileReq] = useState(false)
    const {  getProfileInformations, profileData  } = useProfileContext()
    const { setUserIdToken, setIsUserAuth } = useUserDataContext();

    const SignIn = useCallback( async({email, password}: ISignIn) => {
        const siginRes =  await Auth.signIn(email, password);
        const storagedData = siginRes?.pool?.storage

        for (let i = 0; i < storagedData.length; i += 1) {
            const key = storagedData.key(i)
            if (key.startsWith("CognitoIdentityServiceProvider") && key.endsWith("idToken")) {
                setUserIdToken(storagedData.getItem(key));
            }
        }
        setAmplifyPwd(password);
        if (featureToggles(ctx, "PROFILE")) {
            setHasDoneProfileReq(true)
        }else {
            handleSignInSucces()
        }
    }, [setAmplifyPwd, setUserIdToken])

    const handleSignInSucces = useCallback(() => {
        setIsUserAuth(true)
        addToast({
            type: "success",
            title: successToastMessageTitle,
            description: successToastMessageDescription,
        });

        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "Form Login",
            label: "loggedUser ok"
        });

        if (isModal) {
            if (onCloseClick) {
                onCloseClick();
            }
        } else {
            if (getParam("path")) {
                window.location.href = `${Redirect(ctx.hosts)}${getParam("path")}`
                return
            }
            window.location.href = `${Redirect(ctx.hosts)}/mytravels/reservations`;

        }
    }, [addToast, ctx.hosts, isModal, onCloseClick, saveOnGa, setIsUserAuth, successToastMessageDescription, successToastMessageTitle])

    useEffect(() => {
        if(hasDoneProfileReq){
            setHasDoneProfileReq(false)
            getProfileInformations()
        }

    }, [hasDoneProfileReq])

    const requiredProfileInformations = useMemo(() => {
        return !!profileData?.name && !!profileData?.birthdate && !!profileData?.contact && !!profileData?.cpf
    }, [profileData])

    useEffect(() => {
        if(profileData?.email) {
            setLoading(false);
            if(!requiredProfileInformations) {
                renderNewStep("AlmostThere")
                setPreviousStep("Password")
            }else {
                handleSignInSucces()
            }
        }
    }, [handleSignInSucces, profileData?.email, renderNewStep, requiredProfileInformations, setLoading, setPreviousStep])

    const handleSubmit = useCallback(async (data: IPasswordProps): Promise<void> => {
        try {
            formRef.current?.setErrors({});
            const schema = Yup.object().shape({
                password: Yup.string().required(errorPasswordRequired),
            });

            await schema.validate(data, {
                abortEarly: false
            });

            setLoading(true);
            await SignIn({email: user.email, password: data.password})
        } catch (error) {
            setLoading(false);
            formRef.current?.setErrors({ password: errorMessage });
        }
    }, [SignIn, errorMessage, errorPasswordRequired, setLoading, user.email]);

    const handleMainScreen = () => {
        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "change email",
            label: "action :: change email"
        });
        setUser({
            ...user,
            email: ""
        });
        renderNewStep("MainScreen");
    };

    const handleForgotPassword = () => {
        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "Recovery Password",
            label: "step 1 | forgot password"
        });
        renderNewStep("ForgotPassword");
    };

    if(loading) {
        return <Loading />
    }

    return <>
        <HeaderLogin isModal={isModal} previousStep="MainScreen" onCloseClick={onCloseClick} />
        <div className={classes.root}>
            <Title>
                {translate({ id: "Password.title" })}
            </Title>
            <div className={classes.emails}>
                <SubTitle className={classes.subTitle} >
                    {user ? user.email : ""}
                </SubTitle>
                <Link onClick={(handleMainScreen)} className={classes.cursor}>
                    <SubTitle style={{ marginLeft: "5px" }} className={classes.subTitle} >
                        {linkChangeEmail}
                    </SubTitle>
                </Link>
            </div>

            <Form ref={formRef} handleSubmit={handleSubmit} >
                <Input
                    type="password"
                    name="password"
                    placeholderText={translate({ id: "Password.password" })}
                />
                <InternalButton type="submit" text={intl.formatMessage({ id: "Password.actionButton" })} />
            </Form>

            <Link
                className={`${classes.cursor} ${classes.link}`}
                onClick={() => handleForgotPassword()}
            >
                <SubTitle variant="caption" className={classes.subTitle}>
                    <b>{translate({ id: "Password.forgotPassword" })}</b>
                </SubTitle>
            </Link>

        </div>
        <Terms />
    </>;
};

export default injectIntl(Password);
