/* eslint-disable react/jsx-filename-extension */
import React, { useEffect, useState } from "react";
import {
    Box,
    Dialog,
    DialogContent,
    Grid
} from "@material-ui/core";
import PropTypes from "prop-types";
import { useStyles } from "./styles";

/**
 *  Modal component.
 */
const ModalGeneric = ({
    children,
    testId,
    isOpen,
    onClose
}) => {
    const classes = useStyles({});
    const [_isOpen, _setIsOpen] = useState(isOpen);

    const handleClose = () => {
        _setIsOpen(false);
        onClose();
    };

    useEffect(() => {
        _setIsOpen(isOpen);
    }, [isOpen]);

    return (
        <Dialog
            open={_isOpen}
            PaperProps={{ className: classes.modalDialogWrapper }}
            data-testid={testId}
        >
            <DialogContent>
                <button
                    type="button"
                    className={classes.modalDialogButtonDesktop}
                    onClick={handleClose}
                    data-testid="close"
                >
                    <svg viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg">
                        <path d="M12.5835 0.833252L7.00016 6.41658L1.41683 0.833252C1.25016 0.666585 1.00016 0.666585 0.833496 0.833252C0.666829 0.999919 0.666829 1.24992 0.833496 1.41659L6.41683 6.99992L0.833496 12.5833C0.666829 12.7499 0.666829 12.9999 0.833496 13.1666C1.00016 13.3333 1.25016 13.3333 1.41683 13.1666L7.00016 7.58325L12.5835 13.1666C12.7502 13.3333 13.0002 13.3333 13.1668 13.1666C13.3335 12.9999 13.3335 12.7499 13.1668 12.5833L7.5835 6.99992L13.1668 1.41659C13.3335 1.24992 13.3335 0.999919 13.1668 0.833252C13.0002 0.666585 12.7502 0.666585 12.5835 0.833252Z"/>
                    </svg>
                </button>

                <Grid container className={classes.modalWrapper}>
                    <Box className={classes.modalContentWrapper}>{children}</Box>
                </Grid>
            </DialogContent>
        </Dialog>
    );
};

ModalGeneric.defaultProps = {
    testId: "",
    children: <p>test</p>,
    onClose: () => null
};

ModalGeneric.propTypes = {
    isOpen: PropTypes.bool.isRequired,
    testId: PropTypes.string,
    children: PropTypes.element,
    onClose: PropTypes.func
};

export default (ModalGeneric);
