import React from "react";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import withStyles from "@material-ui/core/styles/withStyles";
import PropTypes from "prop-types";
import { styles } from "./styles";

const CustomCheckbox = ({
    checked, label, classes, onChange, inverted, disabled
}) => {
    const onHandleChange = () => {
        if (onChange) onChange(!checked);
    };

    const checkBoxClasses = {
        root: inverted ? classes.checkboxInverted : classes.checkbox,
        disabled: classes.disabled,
    };

    const formControlClasses = {
        root: inverted ? classes.rootInverted : classes.root,
    };

    const checkboxColor = inverted ? "secondary" : "primary";

    return (
        <FormControlLabel
            classes={formControlClasses}
            control={<Checkbox color={checkboxColor} onChange={onHandleChange} classes={checkBoxClasses} checked={checked} disabled={disabled} />}
            label={label}
        />
    );
};

CustomCheckbox.propTypes = {
    classes: PropTypes.object.isRequired,
    checked: PropTypes.bool,
    label: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    onChange: PropTypes.func,
    inverted: PropTypes.bool,
    disabled: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(CustomCheckbox);
