import React, { useRef, useContext, useCallback, useEffect } from "react";
import { Typography } from "@material-ui/core";
import { FormHandles } from "@unform/core";
import { injectIntl, IntlShape } from "react-intl";
import * as Yup from "yup";
import { BrandContext } from "../../../BrandContext";
import { useProfileContext } from "../../../ProfileContext";
import { IProfileData } from "../../../ProfileContext/types";
import useCognitoIdTokenUpdate from "../../../UserArea/CognitoIdTokenUpdate";
import useGa from "../../../utils/useGa";
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import { useLoadingContext } from "../../contexts/LoadingContext";
import { useRenderSteps } from "../../contexts/renderSteps";
import { useToast } from "../../contexts/Toast";
import BtnsSocialLogin from "../../shared/components/BtnsSocialLogin";
import Form from "../../shared/components/Form";
import HeaderLogin from "../../shared/components/HeaderLogin";
import Input from "../../shared/components/Input";
import InternalButton from "../../shared/components/InternalButton";
import Loading from "../../shared/components/Loading";
import SubTitle from "../../shared/components/SubTitle";
import Terms from "../../shared/components/Terms";
import Title from "../../shared/components/Title";
import { useUserData, IUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import api from "../../shared/services/api";
import getValidationErrors from "../../shared/utils/getValidationErrors";
import useStyles from "./styles";
import { usersInProvider } from "./utils";

interface SubmitFormData {
    email: string;
    name?: string;
    lastName?: string;
    userExists: boolean;
}

interface IInternalLogin {
    intl: IntlShape;
}

interface MainScreenProps extends IInternalLogin {
    onCloseClick?(): void;
    isModal?: boolean;
}

const MainScreen: React.FC<MainScreenProps> = ({ intl, onCloseClick, isModal }) => {
    const placeHolderEmail = translate({ id: "MainScreen.placeholderEmail" });
    const placeHolderErrorEmail = intl.formatMessage({ id: "MainScreen.placeHolderErrorEmail" });
    const placeHolderEmailRequired = intl.formatMessage({ id: "MainScreen.placeHolderEmailRequired" });
    const errorWarningTitle = intl.formatMessage({ id: "MainScreen.errorWarningTitle" });
    const errorWarningDescription = intl.formatMessage({ id: "MainScreen.errorWarningDescription" });
    const formRef = useRef<FormHandles>(null);
    const { saveOnGa } = useGa();

    const { renderNewStep, setPreviousStep } = useRenderSteps();
    const { setAuthAmplify } = useAuthAmplifyContext();
    const { setUser } = useUserData();
    const { setLoading, loading } = useLoadingContext();
    const ctx = useContext(BrandContext);
    const { addToast } = useToast();
    const classes = useStyles({});
    const { deleteCognitoServiceProvider } = useCognitoIdTokenUpdate();
    const { setProfileData } = useProfileContext();
    useEffect(() => {
        deleteCognitoServiceProvider();
        setProfileData({} as IProfileData)
    }, []);

    const submitForm = useCallback(async (formData: SubmitFormData) => {
        try {
            formRef.current?.setErrors({});

            const schema = Yup.object().shape({
                email: Yup.string()
                    .email(placeHolderErrorEmail)
                    .required(placeHolderEmailRequired)
            });

            await schema.validate(formData, {
                abortEarly: false
            });

            saveOnGa({
                event: "login_event",
                category: "Account",
                action: "Form Login",
                label: "login :: email"
            });

            setUser(formData as IUserData);
            setLoading(true);

            let response;
            try {
                const email = encodeURIComponent(formData.email);
                response = await api.get<SubmitFormData>(`/apigw/authentication/${ctx.brand}/user-exists?email=${email}`);
                const user = usersInProvider(response.data.found);

                setPreviousStep("MainScreen");
                if (user?.nextStep) {
                    if (!user.emailConfirmed) {
                        setAuthAmplify("confirmSignUp");
                    }

                    renderNewStep(user.nextStep);
                } else {
                    renderNewStep("ConfirmMainScreen");
                }
            } catch (err) {
                saveOnGa({
                    event: "fluxo_de_cadastro",
                    category: "Account",
                    action: "Registration",
                    label: "alert :: email validation"
                });
                addToast({
                    type: "error",
                    title: errorWarningTitle,
                    description: errorWarningDescription
                });
            }

            setLoading(false);
        } catch (err) {
            if (err instanceof Yup.ValidationError) {
                formRef.current?.setErrors(getValidationErrors(err));

            }
        }
    },
    [addToast, ctx.brand, errorWarningDescription, errorWarningTitle, placeHolderEmailRequired, placeHolderErrorEmail, renderNewStep, setAuthAmplify, setLoading, setUser]);

    return loading ? (
        <Loading />
    ) : (
        <>
            <HeaderLogin isModal={isModal} previousStep="MainScreen" onCloseClick={onCloseClick} />
            <div className={classes.root}>
                <Form ref={formRef} handleSubmit={submitForm}>
                    <Title>{translate({ id: "MainScreen.title" })}</Title>
                    <SubTitle>{translate({ id: "MainScreen.subtitle" })}</SubTitle>
                    <div className={classes.inputWrapper}>
                        <Input name="email" placeholderText={placeHolderEmail} />
                    </div>

                    <InternalButton type="submit" text={translate({ id: "MainScreen.btnLogin" })} />

                    <div className={classes.orSeparateWrapper}>
                        <Typography variant="caption" className={classes.orSeparate}>
                            ou
                        </Typography>
                    </div>
                    <BtnsSocialLogin />
                </Form>
            </div>
            <Terms />
        </>
    );
};

export default injectIntl(MainScreen);
