import React from "react";
import { Wifi, Gym, Parking, Bar, Pool } from "@cvccorp-components/icons";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import Card from "../Card";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RoomCardStoryProps = {
    mode: string,
    brand: BrandType,
    title: string,
    totalPrice: string,
    partialPrice: string,
    totalDays: string,
    parcel: string,
    bedInfo: string,
    pax: number,
    days: number
}

const RoomCardStory = ({
    mode, 
    brand,
    title,
    totalPrice,
    partialPrice,
    totalDays,
    parcel,
    bedInfo,
    pax,
    days,
}: RoomCardStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Room/Card">
                <Typography variant="h3">RoomCard</Typography>
                <br />
                <Divider />
                <br />
                <Grid item>
                    <Card
                        product="hotel"
                        onClick={() => {}}
                        meal
                        onDetail={() => {}}
                        taxesIncluded
                        noRefundable
                        thumb="http://lorempixel.com/output/technics-q-c-48-48-2.jpg"
                        title={title}
                        totalPrice={totalPrice}
                        partialPrice={partialPrice}
                        totalDays={totalDays}
                        parcel={parcel}
                        bedInfo={bedInfo}
                        pax={pax}
                        days={days}
                        amenities={[
                            <Wifi size="small" key="1"/>,
                            <Gym size="small" key="2"/>,
                            <Parking size="small" key="3"/>,
                            <Pool size="small" key="4"/>,
                            <Bar size="small" key="5"/>,
                            <Wifi size="small" key="6"/>,
                            <Gym size="small" key=""/>,
                            <Parking size="small" key="7"/>,
                            <Pool size="small" key="8"/>,
                            <Bar size="small" key="9"/>
                        ]}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        title: "Quarto Standard",
        totalPrice: "200",
        partialPrice: "100",
        totalDays: "2",
        parcel: "12",
        bedInfo: "Uma cama de casal ou 2 de solteiro",
        pax: 2,
        days: 3
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        title: { type: "string" },
        totalPrice: { type: "string" },
        partialPrice: { type: "string" },
        totalDays: { type: "string" },
        parcel: { type: "string" },
        bedInfo: { type: "string" },
        pax: { type: "number" },
        days: { type: "number" }
    }
}

