import { Divider, Grid, Paper } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import React from "react";
import themes from "../../../Theme/themes";
import { styles } from "../../styles";
import FullCalendar from "../FullCalendar";

const useStyles = makeStyles(theme => ({
    ...styles,
    rootBg: {
        background: theme.palette.primary.main,
        boxShadow: theme.shadows[8],
        padding: "30px"
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px"
    }
}));

export const DefaultFullCalendar = ({ brand, mode, isMobile, error, errorMsg, originLabel, destinyLabel, isOutsideRange }) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="calendar">
                <Typography variant="h3">FullCalendar</Typography>
                <br />
                <Divider />
                <br />

                <Grid xs={3}>
                    <FullCalendar classes={classes} onDatesChange={dates => {}} isMobile={isMobile} error={error} errorMsg={errorMsg} originLabel={originLabel} destinyLabel={destinyLabel} isOutsideRange={isOutsideRange} />
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        isMobile: false,
        error: false,
        errorMsg: "Selecione as datas corretamente",
        originLabel: "Data de ida",
        destinyLabel: "Data de volta",
        isOutsideRange: 330
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        isMobile: { type: "boolean" },
        error: { type: "boolean" },
        errorMsg: { type: "string" },
        originLabel: { type: "string" },
        destinyLabel: { type: "string" },
        isOutsideRange: { type: "number" }
    }
};

storiesOf("Components/Calendars/FullCalendar", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } }).add("Default ", args => <DefaultFullCalendar {...args} />, argsControls);
