import React from "react";
import { useRenderSteps } from "../contexts/renderSteps";
import AlmostThere from "./AlmostThere";
import ChangePassword from "./ChangePassword";
import CodeNonReceived from "./CodeNonReceived";
import ConfirmMainScreen from "./ConfirmMainScreen";
import ForgotPassword from "./ForgotPassword";
import MainScreen from "./MainScreen";
import NewRegister from "./NewRegister";
import Password from "./Password";
import ProfileCorp from "./ProfileCorp";
import SocialLoginAccount from "./SocialLoginAccount";
import UnverifiedAccount from "./UnverifiedAccount";
import ValidationCode from "./ValidationCode";

type RenderStepsSwitchProps = {
    onCloseClick?(): void;
    isModal?: boolean;
};

const RenderStepsSwitch: React.FC<RenderStepsSwitchProps> = ({ onCloseClick, isModal }) => {
    const { currentStep } = useRenderSteps();
    switch (currentStep) {
    case "MainScreen":
        return <MainScreen isModal={isModal} onCloseClick={onCloseClick} />;
    case "ConfirmMainScreen":
        return <ConfirmMainScreen isModal={isModal} onCloseClick={onCloseClick} />;
    case "SocialLoginAccount":
        return <SocialLoginAccount isModal={isModal} onCloseClick={onCloseClick} />;
    case "NewRegister":
        return <NewRegister isModal={isModal} onCloseClick={onCloseClick} />;
    case "Password":
        return <Password isModal={isModal} onCloseClick={onCloseClick} />;
    case "ForgotPassword":
        return <ForgotPassword isModal={isModal} onCloseClick={onCloseClick} />;
    case "ValidationCode":
        return <ValidationCode isModal={isModal} onCloseClick={onCloseClick} />;
    case "CodeNonReceived":
        return <CodeNonReceived isModal={isModal} onCloseClick={onCloseClick} />;
    case "ChangePassword":
        return <ChangePassword isModal={isModal} onCloseClick={onCloseClick} />;
    case "UnverifiedAccount":
        return <UnverifiedAccount isModal={isModal} onCloseClick={onCloseClick} />;
    case "ProfileCorp":
        return <ProfileCorp isModal={isModal} onCloseClick={onCloseClick} />;
    case "AlmostThere":
        return <AlmostThere isModal={isModal} onCloseClick={onCloseClick}/>;

    default:
        return null;
    }
};

export default RenderStepsSwitch;
