import { withStyles } from "@material-ui/core/styles";
import moment from "moment";
import PropTypes from "prop-types";
import React, { useCallback, useEffect, useState } from "react";
import DayPickerRangeController from "react-dates/lib/components/DayPickerRangeController";
import { styles } from "../styles";

const NUMBER_OF_MONTHS_SHOWN_ON_DESKTOP = 2;
const MIN_NUMBER_OF_MONTHS_SHOWN_ON_MOBILE = 13; // precisa ser 13 pra contemplar um ano inteiro

const Calendar = ({ minDate, maxDate, startDate, endDate, onDatesChange, isMobile, classes, onFocusChange, focus, isOutsideRange }) => {
    const [focusedInput, setFocusedInput] = useState(focus);
    const [dStart, setStart] = useState(startDate);
    const [dEnd, setEnd] = useState(endDate);
    const [numberOfMonths, setNumberOfMonths] = useState(NUMBER_OF_MONTHS_SHOWN_ON_DESKTOP);

    const setNumberOfMonthsBasedOnScreenTypeAndStartAndEndDates = useCallback((isMobile, startDate, endDate) => {
        const todayMoment = moment();
        if (isMobile) {
            const startDateMoment = moment(startDate);
            const startDateIsBeforeToday = startDateMoment.isBefore(todayMoment);
            if (startDateIsBeforeToday) {
                const startDateDay = startDateMoment.date();
                const todayDay = todayMoment.date();
                const daysOffset = startDateDay > todayDay ? 1 : 0;
                const totalMonthsDistancePassed = todayMoment.diff(startDateMoment, "months");
                const totalNumberOfMonthsUsingPreviousStartDate = totalMonthsDistancePassed + MIN_NUMBER_OF_MONTHS_SHOWN_ON_MOBILE + daysOffset;
                setNumberOfMonths(totalNumberOfMonthsUsingPreviousStartDate);
            } else {
                const endDateMoment = moment(endDate);
                const totalMonthsDistance = endDateMoment.diff(todayMoment, "months");
                const totalNumberOfMonthsUsingFutureEndDate = totalMonthsDistance >= MIN_NUMBER_OF_MONTHS_SHOWN_ON_MOBILE ? totalMonthsDistance + 1 : MIN_NUMBER_OF_MONTHS_SHOWN_ON_MOBILE;
                setNumberOfMonths(totalNumberOfMonthsUsingFutureEndDate);
            }
        } else {
            setNumberOfMonths(NUMBER_OF_MONTHS_SHOWN_ON_DESKTOP);
        }
    }, []);

    const getInitialVisibleMonthByStartDate = useCallback(() => {
        if (isMobile) {
            return minDate || moment();
        }
        if (startDate) {
            const todayMoment = moment();
            const result = todayMoment.isBefore(startDate) ? startDate : todayMoment;
            return result;
        }
        return minDate || moment();
    }, [startDate]);

    const isOutsideRangeVerification = useCallback(
        day => {
            const onlyStartDateIsFilled = startDate && !endDate;
            const todayMoment = moment();
            const startDateIsNotBeforeToday = moment(startDate)?.diff(todayMoment) >= 0;
				if (onlyStartDateIsFilled && startDateIsNotBeforeToday) {
					let defaultOutsideRange = 330;
					if(isOutsideRange === undefined|| isOutsideRange === null ) {
						isOutsideRange = defaultOutsideRange;
					}
					const maxDateBasedOnStartDate = moment(startDate).add(isOutsideRange, "days");
					const isNotAfterStartDate = day?.diff(startDate) <= 0;
					const isAfterMaxDateBasedOnStartDate = maxDateBasedOnStartDate?.diff(day) < 0;
					return isNotAfterStartDate || isAfterMaxDateBasedOnStartDate;
				}
				let defaultMinDate = moment().add(1, "days").startOf("day");
				if(minDate !== undefined) {
					defaultMinDate = minDate;
				}
				const isBeforeMinDate = defaultMinDate?.diff(day) > 0;
				const isAfterMaxDate = maxDate?.diff(day) < 0;
				return isBeforeMinDate || isAfterMaxDate;
        },
        [minDate, maxDate, startDate, endDate]
    );

    const renderDay = day => {
        const tomorrowDay = moment()
            .add(1, "days")
            .format("YYYY-MM-DD");
        const currentDay = day.format("YYYY-MM-DD");
        const startDateIsNotBeforeToday = tomorrowDay === currentDay;
        return startDateIsNotBeforeToday ? (
            <div className="DayPicker__firstDayUnlockedMobileContainer">
                <div className="DayPicker__firstDayUnlockedMobile">{day.format("D")}</div>
            </div>
        ) : (
            day.format("D")
        );
    };

    useEffect(() => {
        setNumberOfMonthsBasedOnScreenTypeAndStartAndEndDates(isMobile, startDate, endDate);
    }, [isMobile, startDate, endDate, setNumberOfMonthsBasedOnScreenTypeAndStartAndEndDates]);

    useEffect(() => {
        setStart(startDate);
        setEnd(endDate);
    }, [startDate, endDate]);

    useEffect(() => {
        setStart(startDate);
        setEnd(endDate);
    }, [startDate, endDate]);

    return (
        <div className={classes.root}>
            <DayPickerRangeController
                renderDayContents={renderDay}
                minDate={minDate}
                maxDate={maxDate}
                startDate={dStart}
                endDate={dEnd}
                onDatesChange={({ startDate, endDate }) => {
                    setStart(startDate);
                    setEnd(endDate);
                    onDatesChange({
                        startDate,
                        endDate
                    });
                }}
                focusedInput={focusedInput}
                onFocusChange={e => {
                    const focus = !e ? "startDate" : "endDate";
                    setFocusedInput(focus);
                    onFocusChange(focus);
                }}
                numberOfMonths={numberOfMonths}
                initialVisibleMonth={getInitialVisibleMonthByStartDate}
                orientation={isMobile ? "verticalScrollable" : "horizontal"}
                isOutsideRange={isOutsideRangeVerification}
            />{" "}
        </div>
    );
};

Calendar.propTypes = {
    onDateChange: PropTypes.func,
    isMobile: PropTypes.bool,
    startDate: PropTypes.date,
    endDate: PropTypes.date,
    isOutsideRange: PropTypes.number
};


export default withStyles(styles, {
    withTheme: true
})(Calendar);
