import React from "react";
import { Paper, Divider, Typography, Grid } from "@material-ui/core";
import { jsxDecorator } from "storybook-addon-jsx";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { Star } from "@cvccorp-components/icons";
import Theme from "../Theme";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
}));

const CategoryIcons = () => {
    const classes = useStyles();

    return (
        <Paper className={classes.root} id="botoes">
            <Typography variant="h3">Ícones de Categorias</Typography>
            <br />
            <Divider />
            <br />
            <Grid container direction="row" justify="flex-start" spacing={2}>
                <Grid item xs={2}>
                    <Star size="medium" color="secondary" noGutter fill />
                    <Star size="medium" color="secondary" noGutter fill />
                    <Star size="medium" color="secondary" noGutter />
                    <Star size="medium" color="secondary" noGutter />
                    <Star size="medium" color="secondary" noGutter />
                    <Typography variant="subtitle1">Star</Typography>
                </Grid>
                <Grid item xs={2}>
                    <Star color="secondary" noGutter fill />
                    <Star color="secondary" noGutter fill />
                    <Star color="secondary" noGutter />
                    <Star color="secondary" noGutter />
                    <Star color="secondary" noGutter />
                    <Typography variant="subtitle1">Star</Typography>
                </Grid>
            </Grid>
        </Paper>
    );
};

storiesOf("Components/Icons", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Ícones de Categorias", () => <CategoryIcons />);
