import React, { useCallback, useEffect, useRef } from "react";
import { FormHandles } from "@unform/core";
import { Auth } from "aws-amplify";
import { injectIntl, IntlShape } from "react-intl";
import * as Yup from "yup";
import {useProfileContext} from "../../../ProfileContext"
import useGa from "../../../utils/useGa";
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import { useRenderSteps } from "../../contexts/renderSteps";
import { useToast } from "../../contexts/Toast";
import Form from "../../shared/components/Form";
import HeaderLogin from "../../shared/components/HeaderLogin";
import Input from "../../shared/components/Input";
import InternalButton from "../../shared/components/InternalButton";
import SubTitle from "../../shared/components/SubTitle";
import Terms from "../../shared/components/Terms";
import Title from "../../shared/components/Title";
import { useUserData } from "../../shared/hooks/UserContext";
import translate from "../../shared/i18n/translate";
import getValidationErrors from "../../shared/utils/getValidationErrors";

interface INewRegister {
    intl: IntlShape;
    onCloseClick?(): void;
    isModal?: boolean;
}

interface SubmitFormData {
    name?: string;
    surName?: string;
    password: string;
    confirmPassword: string;
}

const NewRegister: React.FC<INewRegister> = ({ intl, onCloseClick, isModal }) => {
    const { user } = useUserData();
    const { renderNewStep, setPreviousStep } = useRenderSteps();
    const { setAuthAmplify, setAmplifyPwd } = useAuthAmplifyContext();
    const title = translate({ id: "NewRegister.title" });
    const placeHolderPwd = translate({ id: "NewRegister.placeHolderPwd" });
    const placeHolderConfirmPwd = translate({ id: "NewRegister.placeHolderConfirmPwd" });
    const errorPasswordRequired = intl.formatMessage({ id: "NewRegister.errorPasswordRequired" });
    const errorPasswordMatch = intl.formatMessage({ id: "NewRegister.errorPasswordMatch" });
    const errorMatchPwds = intl.formatMessage({ id: "NewRegister.errorMatchPwds" });
    const successToastMessageTitle = intl.formatMessage({ id: "NewRegister.successToastMessageTitle" });
    const successToastMessageDescription = intl.formatMessage({ id: "NewRegister.successToastMessageDescription" });
    const errorToastMessageTitle = intl.formatMessage({ id: "NewRegister.errorToastMessageTitle" });
    const errorToastMessageDescription = intl.formatMessage({ id: "NewRegister.errorToastMessageDescription" });
    const { saveOnGa } = useGa();
    const {setIsRegisterUserProfileStepToggle} = useProfileContext()

    const formRef = useRef<FormHandles>(null);
    const { addToast } = useToast();

    useEffect(() => {
        setPreviousStep("ConfirmMainScreen");
        setIsRegisterUserProfileStepToggle(true)
    }, [setIsRegisterUserProfileStepToggle, setPreviousStep]);

    const submitForm = useCallback(async (formData: SubmitFormData) => new Promise(async (resolve) => {
        try {
            formRef.current?.setErrors({});
            const { password, confirmPassword } = formData;

            const schema = Yup.object().shape({
                password: Yup.string().matches(/^(?=.*[0-9|#?!@$%^&*-]).{8,}$/, errorPasswordMatch).required(errorPasswordRequired),
            });

            await schema.validate(formData, {
                abortEarly: false,
            });

            if (password !== confirmPassword) {
                formRef.current?.setErrors({ confirmPassword: errorMatchPwds });
                return;
            }
            formRef.current?.setErrors({ confirmPassword: "" });

            const {email} = user;
            const pwd = formData.password;

            await SignUp(email, pwd);

            resolve("Success");
            setAuthAmplify("confirmSignUp");
            saveOnGa({
                event: "login_event",
                category: "Account",
                action: "Registration",
                label: "step 2 | password registration"
            });
            renderNewStep("ValidationCode");
        } catch (err) {
            if (err instanceof Yup.ValidationError) {
                formRef.current?.setErrors(getValidationErrors(err));

            }
        }

    }), []);

    const SignUp = (email, pwd) => new Promise(async (resolve) => {
        try {
            await Auth.signUp({
                username: email,
                password: pwd
            });
            setAmplifyPwd(pwd);
            resolve("Success");
            addToast({
                type: "success",
                title: successToastMessageTitle,
                description: successToastMessageDescription
            });
        } catch (error) {
            addToast({
                type: "error",
                title: errorToastMessageTitle,
                description: errorToastMessageDescription
            });
        }
    });

    return (
        <>
            <HeaderLogin isModal={isModal} previousStep="ConfirmMainScreen" onCloseClick={onCloseClick} />
            <Form ref={formRef} handleSubmit={submitForm}>
                <Title>{title}</Title>
                <Input
                    name="password"
                    placeholderText={placeHolderPwd}
                    type="password"
                    showTip
                    infoMessage={errorPasswordMatch}
                />
                <Input
                    name="confirmPassword"
                    placeholderText={placeHolderConfirmPwd}
                    type="password"
                    showTip
                    infoMessage={errorPasswordMatch}
                />

                <InternalButton text="Entrar" type="submit" />
                <Terms />
            </Form>

        </>
    );
};

export default injectIntl(NewRegister);
