import React, { useRef } from "react";
import {
    GoogleMap,
    Marker,
    withGoogleMap,
    withScriptjs
} from "react-google-maps";
import MarkerClusterer from "react-google-maps/lib/components/addons/MarkerClusterer";
import map from "../assets/img/map.png";
import { Bubbly, CustomInfoBox } from "./styles";
import "./styles.css";

const clusterStyles = [
    {
        url: map,
        height: 40,
        width: 40,
        textColor: "#97007C",
    },
];

const MapGroup = withScriptjs(withGoogleMap(({
    defaultData,
    data,
    zoom,
    active,
    handleSelect,
    handleMarkerCluster,
    gridSize,
    maxZoom,
    minimumClusterSize,
    onZoomChanged,
}) => {
    const mapRefs = useRef(null);

    const onMarkerCluster = cluster => {
        if (handleMarkerCluster) handleMarkerCluster(cluster.getMarkers());
    };

    const handleOnZoomChanged = () => {
        if (onZoomChanged) onZoomChanged(mapRefs.current);
    };

    return data ? (
        <>
            <GoogleMap
                zoom={zoom}
                center={{
                    lat: defaultData.coords.lat,
                    lng: defaultData.coords.lng,
                }}
                options={{
                    streetViewControl: false,
                    fullscreenControl: false,
                    mapTypeControl: false,
                }}
                ref={mapRefs}
                onZoomChanged={handleOnZoomChanged}
            >
                <MarkerClusterer
                    averageCenter
                    enableRetinaIcons
                    gridSize={gridSize}
                    maxZoom={maxZoom}
                    minimumClusterSize={minimumClusterSize}
                    defaultStyles={clusterStyles}
                    onClick={cluster => onMarkerCluster(cluster)}
                >

                    {children ? (
                        <>
                            {children}
                        </>
                    ) : (
                        <>
                            {data &&
                                data.map(item => {
                                    return (
                                        <Marker
                                            title={JSON.stringify(item)}
                                            name={item.id}
                                            key={item.id}
                                            position={{
                                                lat: item.location.coordinates.latitude,
                                                lng: item.location.coordinates.longitude,
                                            }}
                                            visible={false}
                                        >
                                            <InfoBox
                                                options={{
                                                    closeBoxURL: "",
                                                    enableEventPropagation: true,
                                                }}
                                            >
                                                <Bubbly
                                                    active={active === item.id}
                                                    onClick={() => handleSelect(item)}
                                                >
                                                    <>
                                                        R${" "}
                                                        {get(item,
                                                            ["room", "rate", "pricePerDayWithTax"],
                                                            {}).toFixed()}
                                                    </>
                                                </Bubbly>
                                            </InfoBox>
                                        </Marker>
                                    );
                                })}
                        </>
                    )}
                </MarkerClusterer>
            </GoogleMap>
        </>
    ) : null;
}));

export default MapGroup;
