import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid, { GridSpacing } from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import MuiTypography from "@material-ui/core/Typography";
import Typography from "../Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type TypographyStoryProps = {
    mode: string,
    brand: BrandType,
    type: string,
    label: string
}

const TypographyStory = ({
    mode, brand, type, label
}: TypographyStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Typography">
                <MuiTypography variant="h5">Tipografia</MuiTypography>
                <Divider />
                <Grid
                    container
                    direction="column"
                >
                    <Typography type={type}>
                        {label}
                    </Typography>
                    <p><strong>{type}</strong> - Hind</p>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        type: "headline1",
        label: "A viagem dos seus sonhos começa na CVC"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        type: {
            type: "select",
            options: [
                "headline1", 
                "headline2", 
                "headline3", 
                "headline4", 
                "headline5", 
                "headline6", 
                "body1regular", 
                "body2regular",
                "body1semibold", 
                "body2semibold",
                "subtitle",
                "captionregular", 
                "captionsemibold"
            ]
        },
        label: {
            type: "string",
        },
    }
}


storiesOf("Theme/Typography", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <TypographyStory {...args}/>, argsControls);
