import React from "react";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import SearchPackage from "../SearchPackage";
import Theme from "../../Theme";
import "react-dates/lib/css/_datepicker.css";
// import "../../assets/css/calendar.scss";

const useStyles = makeStyles(theme => ({
    root: {
        boxShadow: theme.shadows[24],
        margin: "20px",
        padding: "20px",
    },
}));

const DefaultSearchPackage = () => {
    const classes = useStyles();

    return (
        <>
            <Paper className={classes.root} id="cards">
                <Typography variant="h1">Search Packages</Typography>
                <br />
                <Divider />
                <br />
                <SearchPackage isMobile={false} />
            </Paper>
        </>
    );
};

storiesOf("Components/Searchs/Search Package", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default ", () => <DefaultSearchPackage />);
