import React from "react";
import { Paper } from "@material-ui/core";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import ThemeProviderCustom from "../../ThemeProviderCustom";
import { BrandType } from "../../types/brand";
import mockBrands from "../../utils/brandMock";
import Image from "../Image";

type ImageStoryProps = {
    brand: BrandType
}

const ImageStory = ({
    brand
}: ImageStoryProps) => {
    const fallbackImgUrl = "https://res.cloudinary.com/cvc-corp/image/upload/v1613574585/img-not-found/img-not-found-results.png";

    return (
        <BrandContext.Provider value={mockBrands[brand]}>
            <ThemeProviderCustom>
                <Paper>
                    <Image 
                        src="https://www.mercadoeeventos.com.br/wp-content/uploads/2021/06/CVC-novamarca2-1024x1024.jpg"
                        alt="Image" 
                        width={360} 
                        height={360} 
                    /> 
                </Paper>
            </ThemeProviderCustom>
        </BrandContext.Provider> 

    );
};

const argsControls = {
    args: {
        brand: "CVC",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
    }
}

storiesOf("Components/Image", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <ImageStory {...args}/>, argsControls);
