/* eslint-disable @typescript-eslint/ban-ts-comment */
import React, { useCallback, useState } from "react";
import { Modal, Typography } from "@material-ui/core";
import classnames from "classnames"
import Cookies from "universal-cookie";
import MyTravelLogin from "../../..";
import { useProfileContext } from "../../../../ProfileContext";
import { useUserDataContext } from "../../../../UserContext";
import useGa from "../../../../utils/useGa";
import useStyles from "./styles";

interface ModalLoginProps {
    newLogin?: string;
    children?: React.ReactNode;
    textBtnLogin: string;
    isMobile?: boolean;
}
declare global {
    interface Window {
        hj: any;
    }
}

const ModalLogin: React.FC<ModalLoginProps> = ({ newLogin, textBtnLogin, isMobile }) => {
    const [open, setOpen] = useState(false);
    const props = { isMobile };
    const { saveOnGa } = useGa();
    const classes = useStyles(props);
    const { isRegisterUserProfileStep, setIsRegisterUserProfileStepToggle } = useProfileContext();
    const { logout } = useUserDataContext();

    const closeModal = useCallback(async(event, reason) => {
        saveOnGa({
            event: "header_event",
            category: "Account",
            action: "Click on header options",
            label: "click on header item :: login"
        })
        if (open && window?.hj) {
            window.hj("event", "viewed_homepage_modal_login");
        }
        if (reason === "backdropClick" ) {
            if(isRegisterUserProfileStep) {
                await logout({
                    noReload: true
                })
                setIsRegisterUserProfileStepToggle(false)
                setOpen(!open);
            }
        }else {
            setOpen(!open);
        }

    }, [open, isRegisterUserProfileStep]);

    const openModal = useCallback(() => {
        setOpen(true)
    }, [])

    return (
        <>
            <div className={`${classes.login}`}>
                <a onClick={openModal} className={classnames(classes.loginBtn, {
                    [classes.newLoginBtn]: newLogin
                })}>
                    <Typography className={classes.loginBtnTxt}>
                        {textBtnLogin}
                    </Typography>
                </a>
            </div>
            <Modal open={open} onClose={closeModal} aria-describedby="modal-login">
                <>
                    <MyTravelLogin onCloseClick={() => setOpen(!open)} isModal />
                </>

            </Modal>
        </>
    )
}

export default ModalLogin;
