import React from "react";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import { UserDataProviderCustom } from "../../BrandContext/BrandContextProvider"
import ThemeProviderCustom from "../../ThemeProviderCustom";
import mockBrands from "../../utils/brandMock"
import MyTravelLogin from "../index";
import ModalLogin from "../shared/components/ModalLogin";

const LoginStory = ({ brand, isModal }) => {
    return (
        <BrandContext.Provider value={mockBrands[brand]}>
            <ThemeProviderCustom>
                <UserDataProviderCustom>
                    {isModal ?  <div style={{
                        backgroundColor: "rgb(35, 127, 208)",
                        padding: "0 40px",
                    }}>
                        <ModalLogin textBtnLogin="ENTRAR" /></div> : <MyTravelLogin /> }

                </UserDataProviderCustom>
            </ThemeProviderCustom>
        </BrandContext.Provider>
    );
};

const argsControls = {
    args: {
        brand: "cvc",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        },
        isModal: {
            type: "boolean",
        }
    }
}

storiesOf("Components/Login", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <LoginStory {...args} />, argsControls);
