import React from "react";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import Newsletter from "../Newsletter";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    title: { color: "#fff" },
}));

const NewsletterExample = ({
    mode, brand, isMobile, title, description, message
}) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper classes={{ root: classes[mode] }} id="newsletter">
                <Typography variant="h1">Newsletter</Typography>
                <br />
                <Divider />
                <br />

                <Grid container>
                    <Grid item xs={12} lg={6}>
                        <Newsletter
                            onClick={(data) => console.log(data)}
                            isMobile={isMobile}
                            privacyPolicy="https://www.submarinoviagens.com.br/institucional/politicas-de-privacidade"
                            title={title}
                            description={description}
                            message={message}
                        />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        isMobile: false,
        title: "Título da Newsletter",
        description: "Descrição da Newsletter",
        message: "Mensagem da Newsletter"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        title: { type: "string" },
        description: { type: "string" },
        message: { type: "string" },
    }
}

storiesOf("Components/Newsletter", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <NewsletterExample {...args}/>, argsControls);
