import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { styles } from "./styles";

const AlertDialog = ({ classes, btn, title, actions, content }) => {
    const [open, setOpen] = React.useState(false);

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClose(callBack) {
        if (callBack) {
            callBack();
        }
        setOpen(false);
    }

    return (
        <div>
            <Button variant="outlined" color="primary" onClick={handleClickOpen}>
                {btn}
            </Button>
            <Dialog
                open={open}
                onClose={() => handleClose()}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                classes={{ paper: classes.paper }}
                BackdropProps={{ className: classes.backdrop }}
            >
                <DialogTitle classes={{ root: classes.title }}>{title}</DialogTitle>
                <DialogContent classes={{ root: classes.content }}>
                    <DialogContentText> {content} </DialogContentText>
                </DialogContent>
                <DialogActions classes={{ root: classes.actions }}>
                    {actions.map((item, key) => (
                        <span
                            key={key}
                            onClick={() => handleClose(item.callBack)}
                            color={item.color}
                            className={`${classes.btn} ${classes[item.color]}`}
                        >
                            {item.label}
                        </span>
                    ))}
                </DialogActions>
            </Dialog>
        </div>
    );
};

AlertDialog.propTypes = { classes: PropTypes.object.isRequired };

export default withStyles(styles, { withTheme: true })(AlertDialog);
