import React, { useContext, useEffect, useState } from "react";
import { Amplify } from "aws-amplify";
import { ProfileDataProvider } from "../ProfileContext";
import ThemeProviderCustom from "../ThemeProviderCustom/ThemeProviderCustom";
import { UserDataProvider, useUserDataContext } from "../UserContext";
import configAws from "./amplify/aws.config";
import { BrandContext, BrandContextType, useFetchAllBrands } from "./BrandContext";
import api from "./services/api";

export function getHostName(req?: any): { hostname: string } {
    const isServer = Boolean(req);

    if (!isServer && typeof window === "undefined")
        throw { message: "Você está chamando esse método em um contexto de servidor sem a requisição correspondente." };

    const fullUrl = isServer ? `http://${req.headers["X-Forwarded-Host"] || req.headers.host}/${req.url}` : document.location.href;
    const url = new URL(fullUrl);
    const qs = url.searchParams;
    return { hostname: (qs.has("__contextBrandUrl") ? qs.get("__contextBrandUrl") : url.hostname) as string };
}

export function brandResolver(brands: BrandContextType[], hostname: string): BrandContextType {
    if (hostname == null) hostname = getHostName().hostname;

    const brand = brands.find(x => x.hosts.includes(hostname || ""));
    if (brand == null) throw { message: `hostname ${hostname} not found.` };
    return brand;
}
export function BrandContextProvider({ brand, brands, hostname, children }: { brand?: BrandContextType, brands?: BrandContextType[], hostname: string, children: JSX.Element }) {
    return <BrandContext.Provider value={brand ?? brandResolver(brands!, hostname)}>{children}</BrandContext.Provider>;
}

export const ClientBasedAllBrands = React.createContext(null);
function AllBrandsProvider({ contextPath, children }) {
    const brands = useFetchAllBrands(contextPath);
    return <ClientBasedAllBrands.Provider value={brands}>{children}</ClientBasedAllBrands.Provider>;
}

export function InternalApp({ hostname, children }) {
    const brands = useContext(ClientBasedAllBrands);

    if (brands == null)
        return <div>Carregando...</div>;

    return (
        <BrandContextProvider brands={brands} hostname={hostname}>
            <ThemeProviderCustom>
                <UserDataProviderCustom>
                    <ProfileDataProvider>
                        {children}
                    </ProfileDataProvider>
                </UserDataProviderCustom>
            </ThemeProviderCustom>
        </BrandContextProvider>
    );
}
async function configureAmplify(ctx) {
    const awsAmplifyConfig = await api.get(`/apigw/authentication/${ctx.brand}/config`);
    const awsConfig = configAws(awsAmplifyConfig.data, ctx.hosts);
    Amplify.configure(awsConfig);
    return true
}

export function UserDataProviderCustom({ children }) {
    const [config, setConfig] = useState(false)
    const ctx = useContext(BrandContext);
    useEffect(() => {
        async function fetchConfig() {
            const conf = await configureAmplify(ctx);
            setConfig(conf)
        }
        fetchConfig();
    }, [])
    return (
        <UserDataProvider config={config} >
            <ProfileDataProvider>
                {children}
            </ProfileDataProvider>
        </UserDataProvider>

    )
}
export function ClientBasedApp({ contextPath, hostname, children }) {
    return (
        <AllBrandsProvider contextPath={contextPath}>
            <InternalApp hostname={hostname}>
                {children}
            </InternalApp>
        </AllBrandsProvider>
    );
}
