/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable no-useless-escape */
/* eslint-disable no-extra-boolean-cast */
import React, { useRef, useCallback, useEffect } from "react";
import { FormHandles } from "@unform/core";
import { injectIntl, IntlShape } from "react-intl";
import * as Yup from "yup";
import {useProfileContext} from "../../../ProfileContext"
import { IPatchProfile } from "../../../ProfileContext/types";
import { useUserDataContext } from "../../../UserContext";
import { useToast } from "../../contexts/Toast";
import Checkbox from "../../shared/components/Checkbox";
import Form from "../../shared/components/Form";
import HeaderLogin from "../../shared/components/HeaderLogin";
import Input from "../../shared/components/Input";
import InternalButton from "../../shared/components/InternalButton";
import SubTitle from "../../shared/components/SubTitle";
import Title from "../../shared/components/Title";
import getValidationErrors from "../../shared/utils/getValidationErrors";
import Redirect from "../../shared/utils/Redirect";
import useStyles from "./styles";

interface IFormData {
    name: string
    cpf: string
    birthdate: string
    phone: string
    acceptedMaisCVC: boolean
}

interface AlmostThereProps  {
    onCloseClick?(): void;
    isModal?: boolean;
    intl: IntlShape;
}

const AlmostThere: React.FC<AlmostThereProps> = ({ intl, isModal, onCloseClick }) => {
    const almostThereTitle = intl.formatMessage({ id: "AlmostThere.almostThereTitle" });
    const almostReadyTitle = intl.formatMessage({ id: "AlmostThere.almostReadyTitle" });
    const almostThereSubTitle = intl.formatMessage({ id: "AlmostThere.almostThereSubTitle" });
    const almostReadySubTitle = intl.formatMessage({ id: "AlmostThere.almostReadySubTitle" });
    const inputPlaceholderBirthdate = intl.formatMessage({ id: "AlmostThere.inputPlaceholderBirthdate" });
    const inputPlaceholderCPF = intl.formatMessage({ id: "AlmostThere.inputPlaceholderCPF" });
    const inputInfoCPF = intl.formatMessage({ id: "AlmostThere.inputInfoCPF" });
    const inputPlaceholderPhone = intl.formatMessage({ id: "AlmostThere.inputPlaceholderPhone" });
    const inputPlaceholderName = intl.formatMessage({ id: "AlmostThere.inputPlaceholderName" });
    const errorBirthdateIsRequired = intl.formatMessage({ id: "AlmostThere.errorBirthdateIsRequired" });
    const errorInvalidFormmat = intl.formatMessage({ id: "AlmostThere.errorInvalidFormmat" });
    const errorPhoneIsRequired = intl.formatMessage({ id: "AlmostThere.errorPhoneIsRequired" });
    const errorCpfIsRequired = intl.formatMessage({ id: "AlmostThere.errorCpfIsRequired" });
    const errorNameIsRequired = intl.formatMessage({ id: "AlmostThere.errorNameIsRequired" });
    const termsAndConditionsPart1 = intl.formatMessage({ id: "AlmostThere.termsAndConditionsPart1" });
    const termsAndConditionsPart2 = intl.formatMessage({ id: "AlmostThere.termsAndConditionsPart2" });
    const termsAndConditionsPart3 = intl.formatMessage({ id: "AlmostThere.termsAndConditionsPart3" });
    const termsAndConditionsPart4 = intl.formatMessage({ id: "AlmostThere.termsAndConditionsPart4" });
    const termsAndConditionsPart5 = intl.formatMessage({ id: "AlmostThere.termsAndConditionsPart5" });
    const almostThereActionButton = intl.formatMessage({ id: "AlmostThere.almostThereActionButton" });
    const almostReadyActionButton = intl.formatMessage({ id: "AlmostThere.almostReadyActionButton" });
    const successToastMessageTitle = intl.formatMessage({ id: "AlmostThere.successToastMessageTitle" });
    const successToastMessageDescription = intl.formatMessage({ id: "AlmostThere.successToastMessageDescription" });
    const errorMessageTitle = intl.formatMessage({ id: "AlmostThere.errorMessageTitle" });
    const errorMessageDescription = intl.formatMessage({ id: "AlmostThere.errorMessageDescription" });

    const formRef = useRef<FormHandles>(null);

    const {
        patchProfile,
        profileData,
        setIsPatchProfileScreenToggle,
        isRegisterUserProfileStep,
        setIsRegisterUserProfileStepToggle
    } = useProfileContext()

    const { isUserAuth, isConfig, setIsUserAuth, logout } = useUserDataContext();

    const classes = useStyles({});
    const {addToast} = useToast()

    useEffect(() => {
        setIsRegisterUserProfileStepToggle(true)
    }, [setIsRegisterUserProfileStepToggle]);

    useEffect(() => {
        setIsPatchProfileScreenToggle(true)
    },[setIsPatchProfileScreenToggle])

    useEffect(() => {
        if (isConfig) {
            const isAuth = async () => {
                setIsUserAuth(isUserAuth);
            };
            isAuth();
        }
    }, [isConfig, isUserAuth]);

    const { contact, acceptedMaisCVC, birthdate, name, cpf} = profileData

    useEffect(() => {
        if(name) formRef?.current?.setFieldValue("name", name)
        if (cpf) formRef?.current?.setFieldValue("cpf", cpf);
        if (birthdate){
            const replacedBirthdate = birthdate.replaceAll("-", "/")
            const invertedBirthdate = replacedBirthdate.split("/").reverse().join("/")
            formRef?.current?.setFieldValue("birthdate", invertedBirthdate);
        }
        if(contact && contact.phones && contact.phones.length > 0) formRef?.current?.setFieldValue("phone", contact.phones[0].phoneNumber);
    }, [ birthdate, contact, cpf, name])

    const submitForm =
        useCallback(async (formData: IFormData) => {
            try {
                formRef.current?.setErrors({});

                const schema = Yup.object().shape({
                    name: Yup.string().required(errorNameIsRequired),
                    cpf: Yup.string().min(14, errorInvalidFormmat).required(errorCpfIsRequired),
                    birthdate: Yup.string().min(10, errorInvalidFormmat).required(errorBirthdateIsRequired),
                    phone: Yup.string().required(errorPhoneIsRequired),
                });

                await schema.validate(formData, {
                    abortEarly: false,
                });

                const replacedBirthdate = formData.birthdate.replaceAll("/", "-")
                const invertedBirthdate = replacedBirthdate.split("-").reverse().join("-")

                const formmatedBirthdate = invertedBirthdate

                const postData: IPatchProfile = {
                    name: formData.name,
                    birthdate: formmatedBirthdate,
                    contact: {
                        emails: [],
                        phones: [{
                            type: "PERSONAL",
                            phoneNumber: formData.phone,
                        }]
                    },
                    cpf: formData.cpf,
                    acceptedMaisCVC: formData.acceptedMaisCVC,
                }

                await patchProfile(postData)
                setIsUserAuth(true)
                addToast({
                    type: "success", title: successToastMessageTitle, description: successToastMessageDescription });

                if (isModal) {
                    setTimeout(async() => {
                        if (onCloseClick) {
                            onCloseClick();
                        }
                    }, 3000);
                } else {
                    window.location.href = Redirect();
                }

            } catch (err) {
                addToast({
                    type: "error", title: errorMessageTitle, description: errorMessageDescription });
                if (err instanceof Yup.ValidationError) {
                    formRef.current?.setErrors(getValidationErrors(err));

                }
            }
        }, [addToast, isModal, onCloseClick, patchProfile, setIsUserAuth]);

    return <>
        <HeaderLogin isModal={isModal} previousStep="MainScreen" onCloseClick={async() => {
            if (onCloseClick) {
                await logout({
                    noReload: true
                })
                onCloseClick()
            }
        }} />

        <div className={classes.root}>
            <Form ref={formRef} handleSubmit={submitForm}>
                <Title>
                    {isRegisterUserProfileStep ? almostReadyTitle : almostThereTitle }
                </Title>
                <SubTitle className={classes.subTitle }>
                    { isRegisterUserProfileStep ?
                        almostReadySubTitle :
                        almostThereSubTitle
                    }
                </SubTitle>
                <div className={classes.inputWrapper}>
                    <Input
                        name="name"
                        placeholderText={inputPlaceholderName}
                        shrink={!!name}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <Input
                        name="cpf"
                        placeholderText={inputPlaceholderCPF}
                        hasMask
                        mask={!!cpf ? "\\*\\*\\*.\\*\\*\\*.99\\*-9\\*" : "999.999.999-99"}
                        autoComplete="off"
                        shrink={!!cpf}
                        disabledInput={!!cpf}
                        showTip
                        infoMessage={inputInfoCPF}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <Input
                        name="birthdate"
                        placeholderText={inputPlaceholderBirthdate}
                        hasMask
                        mask="99/99/9999"
                        shrink={!!birthdate}
                    />
                </div>
                <div className={classes.inputWrapper}>
                    <Input
                        name="phone"
                        placeholderText={inputPlaceholderPhone}
                        hasMask
                        mask="(99) 999999999"
                        shrink={(contact && contact?.phones && contact.phones.length > 0)}
                    />

                </div>
                {!acceptedMaisCVC &&
                    <Checkbox
                        name="acceptedMaisCVC"
                        value="consent"
                        label={(<>
                            {`${termsAndConditionsPart1} `}<a target="_blank" href="https://qa.cvc.com.br/cartao-de-credito-cvc" rel="noreferrer"> {`${termsAndConditionsPart2} `}</a>{`${termsAndConditionsPart3} `}<a target="_blank" href="https://cartaoitau.cvc.com.br/maiscvc_regulamento.pdf" rel="noreferrer">{`${termsAndConditionsPart4} `}</a>{`${termsAndConditionsPart5}`}
                        </>)}
                    />
                }
                <div className={classes.internalButtonWrapper}>
                    <InternalButton type="submit" text={isRegisterUserProfileStep ? almostReadyActionButton : almostThereActionButton } />
                </div>
            </Form>
        </div>
    </>

};

export default injectIntl(AlmostThere);
