/* eslint-disable no-alert */
import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import SubmenuMobile from "../SubmenuMobile";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type SubmenuMobileStoryProps = {
    mode: string,
    brand: BrandType,
    isOpen: boolean,
}

const SubmenuMobileStory = ({
    mode, brand, isOpen
}: SubmenuMobileStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="SubmenuMobile">
                <Typography variant="h3">SubmenuMobile</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <SubmenuMobile 
                        menu={[
                            {
                                label: "Opção 1",
                                link: "https://cvc.com.br/",
                                target: "_blank"
                            },
                            {
                                label: "Opção 2",
                                link: "https://cvc.com.br/",
                                target: "_blank"
                            },
                            {
                                label: "Opção 3",
                                link: "https://cvc.com.br/",
                                target: "_blank"
                            }
                        ]} 
                        isOpen={isOpen}
                        clickBack={() => alert("clicado")} 
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        isOpen: true,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        isOpen: {
            type: "boolean"
        },
    }
}

storiesOf("Components/SubmenuMobile", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <SubmenuMobileStory {...args}/>, argsControls);
