/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback, useContext, useEffect, useMemo, useState } from "react";
import { Avatar, useMediaQuery } from "@material-ui/core";
import PropTypes from "prop-types";
import Cookies from "universal-cookie";
import { BrandContext } from "../BrandContext";
import Button from "../Button";
import ModalLogin from "../MyTravelLogin/shared/components/ModalLogin";
import { useProfileContext } from "../ProfileContext";
import Typography from "../Typography";
import { useUserDataContext } from "../UserContext";
import { featureToggles, verifyIsBrand, findCognitoIdToken } from "../utils"
import useGa from "../utils/useGa";
import useCognitoIdTokenUpdate from "./CognitoIdTokenUpdate";
import { UserAreaInfo, UserAreaButtons, UserAreaIcon } from "./components";
import { useStyles } from "./styles"

const Header = ({ user, lightTheme, className }) => {
    const ctx = useContext(BrandContext);
    const desktopSize = useMediaQuery("(min-width:768px)");
    const classes = useStyles({});
    const { saveOnGa } = useGa();
    const { isUserAuth, setIsUserAuth, isConfig, logout } = useUserDataContext();
    const { profileData, getProfileInformations } = useProfileContext();

    const [isLogged, setIsLogged] = useState(false);

    const { cognitoIdToken } = useCognitoIdTokenUpdate();

    const cookies = new Cookies();
    const corpAgentId = cookies.get("corpAgentId");

    useEffect(() => {
        if (isConfig) {
            const isAuth = async () => {
                setIsUserAuth(isUserAuth);
            };
            isAuth();
        }
    }, [isConfig, isUserAuth]);

    const handleClickMyTrip = () => {
        saveOnGa({
            event: "header_event",
            category: "Account",
            action: "Click on header options",
            label: "click on header item :: mytrips"
        });
        window.location.href = "/mytravels/reservations/"
    };

    useEffect(() => {
        setIsLogged(cognitoIdToken);
    }, [cognitoIdToken]);

    const { name, birthdate, contact, cpf } = profileData

    const getProfile = useCallback(async() => {
        const profileResp = await getProfileInformations()
        const hasProfileInfo = !!profileResp?.name && !!profileResp?.birthdate && !!profileResp?.contact && !!profileResp?.cpf
        if(!hasProfileInfo){
            logout({
                noReload: true
            })
        }
    }, [])

    useEffect(() => {
        if(featureToggles(ctx, "PROFILE") && isUserAuth){
            getProfile()
        }
    }, [isUserAuth])

    const requiredProfileInformations = useMemo(() => {
        return !!name && !!birthdate && !!contact && !!cpf
    }, [birthdate, contact, cpf, name])

    const profileInfo = useMemo(() => {
        if(featureToggles(ctx, "PROFILE")){
            return requiredProfileInformations
        }
        return true

    }, [ctx, requiredProfileInformations])

    if (verifyIsBrand(ctx, "Sub") || featureToggles(ctx, "NEW_LOGIN")) {
        if ((!!findCognitoIdToken() || !!cognitoIdToken || !!isLogged || !!corpAgentId) && profileInfo ) {
            return (
                <UserAreaButtons
                    lightTheme={lightTheme}
                    className={className}
                    onClickLogout={() => logout({
                        willBeSavedInGA: true
                    })}
                    onClickMyTravels={handleClickMyTrip}
                    newLogin={featureToggles(ctx, "NEW_LOGIN")}
                    employer={corpAgentId}
                    isCVC={verifyIsBrand(ctx, "CVC")}
                    isMobile={!desktopSize}
                    isLogged
                />
            );
        }

        return (
            <div className={`${classes.loginNoRegister} ${className} login-info-container`}>
                <ModalLogin
                    textBtnLogin={
                        featureToggles(ctx, "NEW_LOGIN")
                            ? <UserAreaIcon
                                isEmployer
                                text={featureToggles(ctx, "NEW_LOGIN") ? "Entre ou cadastre-se" : null}
                                isMobile={!desktopSize}
                            />
                            : "Entrar"
                    }
                    isMobile={!desktopSize}
                    newLogin={featureToggles(ctx, "NEW_LOGIN") ? classes.loginBtn : null}
                />
            </div>
        );
    }

    if (user) {
        return (
            <div className={`${lightTheme ? classes.loggedLight : classes.logged} ${className} login-info-container`}>
                <Avatar className={lightTheme ? classes.avatarLight : classes.avatar} src={user.avatar}>
                    {user.name.slice(0, 2)}
                </Avatar>
                <UserAreaInfo
                    userName={user.name}
                    className={lightTheme ? classes.userLight : classes.user}
                    hiddenMyTravels={!lightTheme}
                    newLogin={featureToggles(ctx, "NEW_LOGIN")}
                />
                {lightTheme && <Button color="primary" label="Sair" size="small" variant="outlined" className={classes.logout} />}
            </div>
        );
    }

    if (lightTheme) {
        return (
            <div className={`${classes.loginNoRegister} ${className} login-info-container`}>
                <div>
                    <UserAreaInfo
                        userName="Viajante"
                        className={classes.hello}
                    />
                </div>
                <div>
                    <Button color="primary" label="Entrar" size="small" variant="outlined" href={ctx.menu.login} />
                </div>
            </div>
        );
    }

    return (
        <div className={`${classes.login}  ${className} login-info-container`}>
            <a className={classes.loginBtn} href={ctx.menu.login}>
                <Typography type="body2semibold" className={classes.loginBtnTxt}>
                    Entrar
                </Typography>
            </a>
        </div>
    );
};

Header.propTypes = {
    user: PropTypes.object,
    lightTheme: PropTypes.bool,
    className: PropTypes.string
};

export default Header;
