import { useCallback } from "react"
import { getHostName } from "../BrandContext/BrandContextProvider";

type DataLayer = {
    event: string;
    category: string;
    action: string;
    label: string;
}

const useGa = () => {
    const saveOnGa = useCallback(async (dataLayer: DataLayer) => {
        if (!getHostName().hostname.includes("local")) {
            if (typeof window !== "undefined") {
                (window as any).dataLayer.push({ ...dataLayer })
            }
        } else {
            // eslint-disable-next-line no-console
            console.info("LOCALHOST: ENVIADO AO GA ==== ", { ...dataLayer });
        }

    }, []);

    return { saveOnGa };
}

export default useGa;
