import React from "react";
import { Paper } from "@material-ui/core";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import ThemeProviderCustom from "../../ThemeProviderCustom";
import mockBrands from "../../utils/brandMock"
import Footer from "../Footer";

const FooterStory = ({ brand }) => {
    
    return (
        <BrandContext.Provider value={mockBrands[brand]}>
            <ThemeProviderCustom>
                <Paper>
                    <Footer /> 
                </Paper>
            </ThemeProviderCustom>
        </BrandContext.Provider>       
    );
};

const argsControls = {
    args: {
        brand: "cvc"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        }
    }
}

storiesOf("Components/Footer", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <FooterStory {...args}/>, argsControls);
