import React from "react";
import { Wifi, Gym, Parking, Pool, Bar } from "@cvccorp-components/icons";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import Amenities from "../Amenities";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RoomAmenitiesStoryProps = {
    mode: string,
    brand: BrandType
}

const RoomAmenitiesStory = ({
    mode, brand
}: RoomAmenitiesStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Room/Amenities">
                <Typography variant="h3">RoomAmenities</Typography>
                <br />
                <Divider />
                <br />
                <Grid >
                    <Amenities 
                        amenities={[
                            <Wifi size="small" key="1"/>,
                            <Gym size="small" key="2"/>,
                            <Parking size="small" key="3"/>,
                            <Pool size="small" key="4"/>,
                            <Bar size="small" key="5"/>,
                            <Wifi size="small" key="6"/>,
                            <Gym size="small" key="7"/>,
                            <Parking size="small" key="8"/>,
                            <Pool size="small" key="9"/>,
                            <Bar size="small" key="10"/>
                        ]}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
    }
}

storiesOf("Components/Room/Amenities", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <RoomAmenitiesStory {...args}/>, argsControls);
