import React from "react";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import { jsxDecorator } from "storybook-addon-jsx";
import { muiTheme } from "storybook-addon-material-ui";
import Theme from "../../Theme";
import Modal from "../Modal";
import { themeOverWrites } from "./Modal.stories.styles";

const useStyles = makeStyles(themeOverWrites);

type ModalStoryProps = {
    mode: string;
    content: string;
    title: string;
    onOpen: () => void;
    onClose: () => void;
    isMobile: boolean;
};

const ModalStory = ({ mode, content, title, onOpen, onClose, isMobile }: ModalStoryProps): React.ReactElement => {
    const classes = useStyles();
    
    return (
        <Paper className={classes[mode]} id="badge">
            <Typography variant="h3">Modal</Typography>
            <br />
            <Divider />
            <br />
            <Grid container>
                <Grid item xs={12} lg={6}>
                    <Modal onClose={onClose} onOpen={onOpen} title={title} isMobile={isMobile}>
                        <p>{content}</p>
                    </Modal>
                </Grid>
            </Grid>
        </Paper>
    );
};

const argsOptions = {
    args: {
        mode: "whiteBg",
        content: `Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed id ex rhoncus, dictum
         risus mattis, lobortis nisi. Suspendisse aliquam diam at sodales blandit.`,
        title: "Exemplo de título",
        isMobile: false
    },
    argTypes: {
        onOpen: { action: "onOpen" },
        onClose: { action: "onClose" },
        content: { type: "text" },
        title: { type: "text" },
        mode: {
            type: "select",
            options: ["whiteBg", "darkBg"]
        },
        isMobile: { type: "boolean" }
    }
};

storiesOf("Components/Modal", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default", args => <ModalStory {...args} />, argsOptions);
