import React from "react";
import { Paper, Divider, Typography, Grid } from "@material-ui/core";
import { jsxDecorator } from "storybook-addon-jsx";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { Facebook, Instagram, Pinterest, Twitter, YouTube } from "@cvccorp-components/icons";
import Theme from "../Theme";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
}));

const SocialNetworksIcons = () => {
    const classes = useStyles();

    return (
        <Paper className={classes.root} id="botoes">
            <Typography variant="h3">Ícones de redes sociais</Typography>
            <br />
            <Divider />
            <br />
            <Grid container direction="row" justify="flex-start" spacing={2}>
                <Grid item xs={1}>
                    <Facebook size="medium" />
                    <Typography variant="subtitle1">Facebook</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Instagram size="medium" />
                    <Typography variant="subtitle1">Instagram</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Pinterest size="medium" />
                    <Typography variant="subtitle1">Pinterest</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Twitter size="medium" />
                    <Typography variant="subtitle1">Twitter</Typography>
                </Grid>
                <Grid item xs={1}>
                    <YouTube size="medium" />
                    <Typography variant="subtitle1">Youtube</Typography>
                </Grid>
            </Grid>
        </Paper>
    );
};

storiesOf("Components/Icons", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Ícones de redes sociais", () => <SocialNetworksIcons />);
