import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Outlet from "../Outlet";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type OutletStoryProps = {
    mode: string,
    brand: BrandType
    isMobile: boolean
}

const OutletStory = ({
    mode, brand, isMobile
}: OutletStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Outlet">
                <Typography variant="h3">Outlet</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={isMobile ? 2 : 12}>
                    <Outlet
                        classes={classes}
                        isMobile={isMobile}
                        onclick={() => {}}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        isMobile: false,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        isMobile: { type: "boolean" }
    }
}

storiesOf("Components/Outlet", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <OutletStory {...args}/>, argsControls);
