/* eslint-disable react/jsx-no-duplicate-props */
import React, { useCallback, useEffect, useRef, useState } from "react";
import TextField from "@material-ui/core/TextField/TextField";
import VisibilityOffOutlined from "@material-ui/icons/VisibilityOffOutlined";
import VisibilityOutlined from "@material-ui/icons/VisibilityOutlined";
import { useField } from "@unform/core";
import ReactInputMask, { Props as InputMaskProps } from "react-input-mask";
import MessageValidation from "../MessageValidation";
import useStyles from "./styles";

interface InputProps extends Omit<InputMaskProps, "mask"> {
    name: string;
    placeholderText: React.ReactElement | null | string;
    type?: "number" | "email" | "text" | "password" | "hidePassword";
    maxLength?: number;
    showTip?: boolean;
    infoMessage?: string;
    hasMask?: boolean;
    mask?: string | (string | RegExp)[]
    disabledInput?: boolean
    shrink?: boolean
}

const Input: React.FC<InputProps> = ({
    name,
    type,
    maxLength,
    placeholderText,
    showTip,
    infoMessage,
    hasMask,
    mask,
    disabledInput,
    shrink,
    ...rest
}) => {
    const { fieldName, registerField, error, defaultValue } = useField(name);
    const classes = useStyles({ error });
    const inputRef = useRef<any>(null);
    const errorMessageRef = useRef<HTMLInputElement>(null);
    const infoMessageRef = useRef<HTMLInputElement>(null);
    const [showInfoMessage, setShowInfoMessage] = useState(false);
    const [inputType, setInputType] = useState(type);
    const [shrinkInput, setShrinkInput] = useState(shrink)

    useEffect(() => {
        registerField({
            name: fieldName,
            ref: inputRef.current,
            path: "value",
        });
    }, [fieldName, registerField]);

    const handleVisibility = useCallback((ref) => {
        if (ref.current.type === "password") {
            ref.current.type = "text";
            setInputType("hidePassword");
        } else {
            ref.current.type = "password";
            setInputType("password");
        }
    }, []);

    const handleShowInfoMessage = useCallback(() => {
        setShrinkInput(true)
        if (errorMessageRef && errorMessageRef.current) {
            errorMessageRef.current.value = "";
        }
        if (error && error.split("").length > 0) {
            setShowInfoMessage(false);
        } else {
            setShowInfoMessage(showTip === true);
        }
    }, []);

    const handleHideInfoMessage = useCallback(() => {
        setShowInfoMessage(false);
    }, []);

    if(hasMask){
        return <>
            <ReactInputMask
                ref={inputRef}
                defaultValue={defaultValue}
                value={defaultValue}
                disabled={disabledInput}
                mask={mask || ""}
                onChange={handleHideInfoMessage}
                onFocus={handleShowInfoMessage}
            >
                {() =>  <TextField
                    name={name}
                    className={classes.input}
                    inputRef={inputRef}
                    margin="dense"
                    defaultValue={defaultValue}
                    label={placeholderText}
                    variant="outlined"
                    type={inputType}
                    disabled={disabledInput}
                    onClick={() => setShrinkInput(true)}
                    value={defaultValue}
                    InputLabelProps={{
                        style: error ? { color: "#FF3344" } : { color: "" },
                        shrink: shrinkInput,
                    }}
                    InputProps={{
                        classes: {
                            root: classes.outlinedInput,
                            adornedEnd: classes.adornedEnd,
                            focused: classes.isFocused,
                            notchedOutline: error ? classes.notchedOutline : ""
                        },
                    }}
                />
                }
            </ReactInputMask>
            {error && (
                <MessageValidation ref={errorMessageRef} isError message={error} />
            )}
            {!error && type === "password" && showInfoMessage && (
                <MessageValidation ref={infoMessageRef} isInfo message={infoMessage || ""} />
            )}
            {!error && name === "cpf" && showInfoMessage && (
                <MessageValidation ref={infoMessageRef} isInfo message={infoMessage || ""} />
            )}
        </>
    }

    return (
        <>
            <TextField
                name={name}
                className={classes.input}
                inputRef={inputRef}
                margin="dense"
                label={placeholderText}
                variant="outlined"
                type={inputType}
                onChange={handleHideInfoMessage}
                onFocus={handleShowInfoMessage}
                onClick={() => setShrinkInput(true)}
                inputProps={{
                    maxLength
                }}
                InputLabelProps={{
                    style: error ? { color: "#FF3344" } : { color: "" },
                    shrink: shrinkInput,
                }}
                InputProps={{
                    endAdornment: <>
                        {(inputType === "password" && showTip === true) &&
                            <VisibilityOffOutlined onClick={() => handleVisibility(inputRef)}
                                className={classes.inputVisibilityIcon}
                            />
                        }
                        {inputType === "hidePassword" &&
                            <VisibilityOutlined onClick={() => handleVisibility(inputRef)}
                                className={classes.inputVisibilityIcon}
                            />
                        }
                    </>,
                    classes: {
                        root: classes.outlinedInput,
                        adornedEnd: classes.adornedEnd,
                        focused: classes.isFocused,
                        notchedOutline: error ? classes.notchedOutline : ""
                    },
                }}
            />

            {error && (
                <MessageValidation ref={errorMessageRef} isError message={error} />
            )}
            {!error && type === "password" && showInfoMessage && (
                <MessageValidation ref={infoMessageRef} isInfo message={infoMessage || ""} />
            )}

            {!error && name === "cpf" && showInfoMessage && (
                <MessageValidation ref={infoMessageRef} isInfo message={infoMessage || ""} />
            )}
        </>
    );
};

export default Input;
