/* eslint-disable no-alert */
import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import ErrorTimeout from "../ErrorTimeout";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type ErrorTimeoutStoryProps = {
    mode: string,
    brand: BrandType,
    labelButton: string
}

const ErrorTimeoutStory = ({
    mode, brand, labelButton
}: ErrorTimeoutStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="ErrorTimeout">
                <Typography variant="h3">ErrorTimeout</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <ErrorTimeout classes={classes[mode]} labelButton={labelButton} onClick={() => alert("clicado")}/>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        labelButton: "Voltar para página"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        labelButton: { type: "string" }
    }
}

storiesOf("Components/ErrorTimeout", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <ErrorTimeoutStory {...args}/>, argsControls);
