import GAEvents from "./GAEvents";

var keys = {
    "aluguel-casas": () => {
        GAEvents.SaveAluguelCasa();
    }
};

const GAFactory = name => {
    try {
        if (name) {
            var item = keys[name];
            if (item) item();
        }
    } catch (err) {
        console.error("error send to ga");
    }
};

export { GAFactory };
