import React from "react";
import { User, Local } from "@cvccorp-components/icons";
import { Paper, Divider, Typography, Grid, MenuItem } from "@material-ui/core";
import { makeStyles , MuiThemeProvider  } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import Select from "..";
import themes from "../../Theme/themes";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
  ...styles,
  root: {
    background: theme.palette.background.default,
    boxShadow: theme.shadows[8],
    padding: "30px",
  },
  darkBg: {
    background: "rgba(0, 0, 0, 0.6)",
    boxShadow: theme.shadows[8],
    padding: "30px",
  }}));

const SelectStories = ({ brand, mode, label, size }) => {
  const classes = useStyles(themes[brand]);

  const options = [
    {
      value: "Option 1",
      label: "Option 1",
    },
    {
      value: "Option 2",
      label: "Option 2",
    },
    {
      value: "Option 3",
      label: "Option 3",
    },
  ];

  return (
    <MuiThemeProvider theme={themes[brand]}>
      <Paper className={classes[mode]}>
        <Typography variant="h3">Selects</Typography>
        <br />
        <Divider />
        <br />
        <Grid container direction="row" justify="flex-start" spacing={2}>
          <Grid item xs={4}>
            <Select
              label={label}
              size={size}
              icon={<User size="small" />}
              select={options}
            />
          </Grid>
          <Grid item xs={4}>
            <Select label={label} size={size}>
              {options.map(item => (
                <MenuItem key={item.label} value={item.value}>
                  <Local />
                  <Typography md="caption1" sm="caption1">
                    {item.value}
                  </Typography>
                </MenuItem>
              ))}
            </Select>
          </Grid>
        </Grid>
      </Paper>
    </MuiThemeProvider>
  );
};

const argsControls = {
  args: {
    brand: "CVC",
    mode: "root",
    label: "Select",
    size: "small"
  },
  argTypes: {
    brand: {
      type: "select",
      options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
    },
    mode: {
      options: ["root", "darkBg"],
      control: { type: "radio" }
    },
    label: { type: "string" },
    size: {
      options: ["small", "large"],
      control: { type: "radio" }
    },
  }
};

storiesOf("Components/Inputs/Selects", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
  .add("Controls", (args) => <SelectStories {...args}/>, argsControls);
