import React from "react";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import DownloadApp from "../DownloadApp";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type DownloadAppStoryProps = {
    brand: BrandType,
    mode: string,
}

export const DownloadAppStory = ({ brand, mode }: DownloadAppStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="loading">
                <Typography variant="h3">Download App</Typography>
                <br />
                <Divider />
                <br />

                <Grid container spacing={2}>
                    <Grid item xs={12} lg={6}>
                        <DownloadApp />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
    }
};

storiesOf("Components/DownloadApp", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
    .add("Default", (args) => <DownloadAppStory {...args} />, argsControls);
