import React, { createContext, ReactNode, useCallback, useContext, useEffect, useState } from "react";
import { Auth } from "aws-amplify";
import { AuthClass } from "aws-amplify/dist/aws-amplify"
import Cookies from "universal-cookie";
import useGa from "../utils/useGa";

interface ILogout {
    noReload?: boolean
    willBeSavedInGA?: boolean
}

interface IUserDataContext {
    currentUser: any
    isUserAuth: boolean;
    setIsUserAuth(isUserAuth): void;
    Auth: AuthClass;
    isConfig: boolean;
    setIsConfig(): Promise<void>
    setUserIdToken(idToken: string): void
    userIdToken: string
    logout(iLogout?: ILogout): Promise<void>
}

declare global {
    interface Window {
        fingerprint: any;
    }
}

interface IUserProvider {
    config: any;
    children: ReactNode;
}

const UserDataContext = createContext<IUserDataContext>({} as IUserDataContext);

const UserDataProvider: React.FC<IUserProvider> = ({ config, children }: IUserProvider) => {
    const [isConfig, setisConfig] = useState(false);
    const [isUserAuth, setisUserAuth] = useState(false);
    const [currentUser, setCurrentUser] = useState({});
    const [userIdToken, setIdToken] = useState("")

    const cookies = new Cookies();
    const { saveOnGa } = useGa();

    useEffect(() => {
        if (config) {
            setIsConfig()
        }
    }, [config])

    const setIsConfig = async () => {
        try {
            await Auth.currentAuthenticatedUser();
            setisUserAuth(true);
            const { attributes } = await Auth.currentAuthenticatedUser();
            setCurrentUser(attributes);
        } catch (error) {
            setisUserAuth(false);
        }
        setisConfig(true);
    }

    const setIsUserAuth = (isAuth) => {
        setisUserAuth(isAuth);

    }

    const setUserIdToken = useCallback((idToken: string) => {
        setIdToken(idToken)
    }, [])

    const logout = useCallback(async ({noReload, willBeSavedInGA}: ILogout) => {
        await Auth.signOut();
        setIsUserAuth(false);

        cookies.remove("corpAgentId", {
            path: "/"
        });
        cookies.remove("agentId", {
            path: "/"
        });
        cookies.remove("branchId", {
            path: "/"
        });
        cookies.remove("access_token", {
            path: "/"
        });

        if (typeof window !== "undefined" && window.fingerprint) {
            window.fingerprint.removeUserId();
        }

        localStorage.removeItem("user_email");

        if(willBeSavedInGA) {
            saveOnGa({
                event: "header_event",
                category: "Account",
                action: "Click on header options",
                label: "click on header item :: logout"
            });
        }
        if(!noReload){
            window.location.reload();
        }

    }, [saveOnGa])

    return (
        <UserDataContext.Provider value={{
            currentUser,
            isUserAuth,
            setIsUserAuth,
            Auth,
            isConfig,
            setIsConfig,
            setUserIdToken,
            userIdToken,
            logout
        }}>
            {children}
        </UserDataContext.Provider>
    );
};

function useUserDataContext(): IUserDataContext {
    const context = useContext(UserDataContext);
    return context;
}

export { UserDataProvider, useUserDataContext };
