import React, { useEffect } from "react";
import Link from "@material-ui/core/Link";
import useStyles from "./styles";
import translate from "../../shared/i18n/translate";
import Terms from "../../shared/components/Terms";
import { useRenderSteps } from "../../contexts/renderSteps";
import Title from "../../shared/components/Title";
import SubTitle from "../../shared/components/SubTitle";
import { useUserData } from "../../shared/hooks/UserContext";
import InternalButton from "../../shared/components/InternalButton";
import { injectIntl, IntlShape } from "react-intl";
import { Auth } from "aws-amplify";
import { useAuthAmplifyContext } from "../../contexts/AmplifyContext";
import HeaderLogin from "../../shared/components/HeaderLogin";
import useGa from '../../../utils/useGa';

interface IForgotPassword {
    intl: IntlShape;
    onCloseClick?(): void;
    isModal?: boolean;
}

const ForgotPassword: React.FC<IForgotPassword> = ({ intl, onCloseClick, isModal }) => {

    const classes = useStyles({});
    const { saveOnGa } = useGa();
    const { renderNewStep, setPreviousStep } = useRenderSteps();
    const { setAuthAmplify } = useAuthAmplifyContext();
    const { user, setUser } = useUserData();
    useEffect(() => {
        setPreviousStep("Password");
    }, []);
    const handleValidationCode = () => new Promise(async (resolve) => {
        try {
            await Auth.forgotPassword(user.email);
            saveOnGa({
                event: "login_event",
                category: "Account",
                action: "Recovery Password",
                label: "step 2 | redefine password"
            });
            setAuthAmplify("ChangePassword");
            renderNewStep("ValidationCode");
        } catch (error) {
            // exibir error servidor
        }
        return resolve;


    });
    const handleMainScreen = () => {
        setUser({
            ...user,
            email: ""
        });

        saveOnGa({
            event: "login_event",
            category: "Account",
            action: "Change email",
            label: "action :: change email"
        });
        renderNewStep("MainScreen");
    };
    return (
        <>
            <div className={classes.root}>
                <HeaderLogin isModal={isModal} previousStep="Password" onCloseClick={onCloseClick} />
                <Title className={classes.title}>
                    {translate({ id: "ForgotPassword.title" })}
                </Title>
                <SubTitle className={classes.subTitle}>
                    {translate({ id: "ForgotPassword.subTitle" })}
                </SubTitle>
                <SubTitle variant="caption" className={classes.subTitleNoMargin}>
                    {user ? user.email : ""}
                </SubTitle>
                <Link onClick={(handleMainScreen)} className={classes.changeEmail}>
                    <SubTitle variant="caption" className={classes.subTitleLinkChangeEmail}>
                        {translate({ id: "ForgotPassword.linkChangeEmail" })}
                    </SubTitle>
                </Link>

                <div className={classes.buttonContainer}>
                    <InternalButton
                        text={intl.formatMessage({ id: "ForgotPassword.actionButton" })}
                        handlerClick={handleValidationCode}
                    />
                </div>

            </div>
            <Terms />
        </>
    );
};

export default injectIntl(ForgotPassword);
